<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
import Emitter from '../../mixins/emitter'

export default {
  name: 'CheckboxGroup',

  mixins: [ Emitter ],

  props: {
    // 模型
    value: {
      type: Array,
      default () {
        return []
      }
    },
    // 是否垂直排列
    vertical: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      currentValue: this.value
    }
  },

  computed: {
    classes () {
      return [
        'x-checkbox-group',
        {
          'x-checkbox-group--vertical': this.vertical
        }
      ]
    }
  },

  mounted () {
    this.updateModel(true)
  },

  watch: {
    value () {
      this.updateModel(true)
    }
  },

  methods: {
    /**
     * 更新值
     * @param  {Boolean} update 是否更新
     */
    updateModel (update) {
      const value = this.value
      this.$children.forEach(child => {
        child.model = value
        if (update) {
          child.currentValue = value.indexOf(child.label) >= 0
          child.group = true
        }
      })
    },

    /**
     * 处理值改变
     * @param  {Array} data 值
     */
    onChange (data) {
      this.currentValue = data
      this.$emit('input', data)
      this.$emit('change', data)
      // 在表单中使用还需要抛表单改变的事件
      this.dispatch('XFormItem', 'fieldChange')
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-checkbox-group
  font-size font-size-small

  // 垂直布局
  &--vertical
    .x-checkbox-wrapper
      display block
      height 30px
      line-height 30px

  &-item
    display inline-block
</style>



