<template>
  <div :class="classes">
    <scroll-view :suppress-scroll-x="true" :class="[baseClass+ '__list']" ref="hours">
      <ul :class="[baseClass + '__ul']" @click="handleClickHours">
        <li :class="getCellCls(item)" v-for="(item, hourIndex) in hoursList" v-show="!item.hide" :index="hourIndex">{{ formatTime(item.text) }}</li>
      </ul>
    </scroll-view>
    <scroll-view :suppress-scroll-x="true" :class="[baseClass+ '__list']" ref="minutes">
      <ul :class="[baseClass + '__ul']" @click="handleClickMinutes">
        <li :class="getCellCls(item)" v-for="(item, minuteIndex) in minutesList" v-show="!item.hide" :index="minuteIndex">{{ formatTime(item.text) }}</li>
      </ul>
    </scroll-view>
    <scroll-view :suppress-scroll-x="true" :class="[baseClass+ '__list']" v-show="showSeconds" ref="seconds">
      <ul :class="[baseClass + '__ul']" @click="handleClickSeconds">
        <li :class="getCellCls(item)" v-for="(item, secondIndex) in secondsList" v-show="!item.hide" :index="secondIndex">{{ formatTime(item.text) }}</li>
      </ul>
    </scroll-view>
  </div>
</template>
<script>
import ScrollView from '../../scroll-view/scroll-view'
import Options from '../time-mixins'
// import { deepCopy, scrollTop, firstUpperCase } from 'utils'
import firstUpperCase from '../../../utils/string/first-uppercase'
import scrollTop from '../../../utils/dom/scroll-top'
import cloneDeep from 'lodash/cloneDeep'

const baseClass = 'x-time-picker-cells'

export default {
  mixins: [Options],

  components: {
    ScrollView
  },

  props: {
    hours: {
      type: [Number, String],
      default: 0
    },
    minutes: {
      type: [Number, String],
      default: 0
    },
    seconds: {
      type: [Number, String],
      default: 0
    },
    showSeconds: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      baseClass,
      compiled: false
    }
  },
  computed: {
    classes () {
      return [
        `${baseClass}`,
        {
          [`${baseClass}--with-seconds`]: this.showSeconds
        }
      ]
    },
    hoursList () {
      let hours = []
      const hourTmpl = {
        text: 0,
        selected: false,
        disabled: false,
        hide: false
      }

      for (let i = 0; i < 24; i++) {
        // const hour = deepCopy(hourTmpl)
        const hour = cloneDeep(hourTmpl)
        hour.text = i

        if (this.disabledHours.length && this.disabledHours.indexOf(i) > -1) {
          hour.disabled = true
          if (this.hideDisabledOptions) hour.hide = true
        }
        if (this.hours === i) hour.selected = true
        hours.push(hour)
      }

      return hours
    },
    minutesList () {
      let minutes = []
      const minuteTmpl = {
        text: 0,
        selected: false,
        disabled: false,
        hide: false
      }

      for (let i = 0; i < 60; i++) {
        // const minute = deepCopy(minuteTmpl)
        const minute = cloneDeep(minuteTmpl)
        minute.text = i

        if (this.disabledMinutes.length && this.disabledMinutes.indexOf(i) > -1) {
          minute.disabled = true
          if (this.hideDisabledOptions) minute.hide = true
        }
        if (this.minutes === i) minute.selected = true
        minutes.push(minute)
      }

      return minutes
    },
    secondsList () {
      let seconds = []
      const secondTmpl = {
        text: 0,
        selected: false,
        disabled: false,
        hide: false
      }

      for (let i = 0; i < 60; i++) {
        // const second = deepCopy(secondTmpl)
        const second = cloneDeep(secondTmpl)
        second.text = i

        if (this.disabledSeconds.length && this.disabledSeconds.indexOf(i) > -1) {
          second.disabled = true
          if (this.hideDisabledOptions) second.hide = true
        }
        if (this.seconds === i) second.selected = true
        seconds.push(second)
      }

      return seconds
    }
  },
  methods: {
    getCellCls (cell) {
      return [
        `${baseClass}__cell`,
        {
          [`${baseClass}__cell--selected`]: cell.selected,
          [`${baseClass}__cell--disabled`]: cell.disabled
        }
      ]
    },
    handleClickHours (event) {
      this.handleClick('hours', event)
    },
    handleClickMinutes (event) {
      this.handleClick('minutes', event)
    },
    handleClickSeconds (event) {
      this.handleClick('seconds', event)
    },
    handleClick (type, event) {
      const target = event.target
      if (target.tagName === 'LI') {
        const cell = this[`${type}List`][parseInt(event.target.getAttribute('index'))]
        if (cell.disabled) return
        const data = {}
        data[type] = cell.text
        this.$emit('on-change', data)
      }
      this.$emit('on-pick-click')
    },
    scroll (type, index) {
      const from = this.$refs[type].scrollTop
      const to = 24 * this.getScrollIndex(type, index)
      scrollTop(this.$refs[type].$el, from, to, 500)
    },
    getScrollIndex (type, index) {
      const Type = firstUpperCase(type)
      const disabled = this[`disabled${Type}`]
      if (disabled.length && this.hideDisabledOptions) {
        let _count = 0
        disabled.forEach(item => item <= index ? _count++ : '')
        index -= _count
      }
      return index
    },
    updateScroll () {
      const times = ['hours', 'minutes', 'seconds']
      this.$nextTick(() => {
        times.forEach(type => {
          this.$refs[type].updateScrollTop(24 * this.getScrollIndex(type, this[type]))
        })
      })
    },
    formatTime (text) {
      return text < 10 ? '0' + text : text
    }
  },
  watch: {
    hours (val) {
      if (!this.compiled) return
      this.scroll('hours', val)
    },
    minutes (val) {
      if (!this.compiled) return
      this.scroll('minutes', val)
    },
    seconds (val) {
      if (!this.compiled) return
      this.scroll('seconds', val)
    }
  },
  mounted () {
    this.updateScroll()
    this.$nextTick(() => { this.compiled = true })
  }
}
</script>



