<template>
  <div
    class="x-tooltip"
    @mouseenter="showPopper"
    @mouseleave="hidePopper">
    <div class="x-tooltip__rel" ref="reference">
      <slot></slot>
    </div>
    <transition name="fade">
      <div
        class="x-tooltip__popper"
        ref="popper"
        v-show="!disabled && (visible || always)">
        <div class="x-tooltip__content">
          <div class="x-tooltip__inner">
            <slot name="content">{{ content }}</slot>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Popper from '../base/popper'
import oneOf from '../../utils/collection/one-of'

export default {
  name: 'Tooltip',

  mixins: [Popper],

  props: {
    // 提示框出现的位置
    placement: {
      validator (value) {
        return oneOf(value, ['top', 'top-start', 'top-end', 'bottom', 'bottom-start', 'bottom-end', 'left', 'left-start', 'left-end', 'right', 'right-start', 'right-end'])
      },
      default: 'bottom'
    },
    // 显示的内容
    content: {
      type: [String, Number],
      default: ''
    },
    theme: {
      validator (val) {
        return oneOf(val, ['light', 'dark'])
      },
      default: 'dark'
    },
    // 弹出框小箭头箭头是否可见
    arrowVisible: {
      default: true
    },
    // 是否禁用提示框
    disabled: {
      type: Boolean,
      default: false
    },
    // 延迟显示，单位毫秒
    delay: {
      type: Number,
      default: 0
    },
    // 如果此选项设置为true，则当鼠标从弹出信息框中移开时不关闭信息框
    controlled: {
      type: Boolean,
      default: false
    },
    // 是否一直显示
    always: {
      type: Boolean,
      default: false
    }
  },

  methods: {
    /**
     * 显示弹出信息框
     */
    showPopper () {
      this.timeout = setTimeout(() => {
        this.visible = true
      }, this.delay)
    },

    /**
     * 隐藏弹出信息框
     */
    hidePopper () {
      clearTimeout(this.timeout)
      if (!this.controlled) {
        this.visible = false
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/popper'

.x-tooltip
  display inline-block

  /*&__rel
    display inline-block
    position relative*/

  &__popper
    z-index zindex-tooltip

  &__inner
    max-width tooltip-max-width
    min-height 20px
    padding 8px 12px
    text-align left
    text-decoration none
    border-radius btn-border-radius-base
    white-space nowrap

    p
      margin 5px 0

for theme in themes
  .theme-{theme}
    .x-tooltip
      &__popper
        popper(tooltip-arrow-width, tooltip-distance, lookup(theme + '-tooltip-bg'))
      &__inner
        color lookup(theme + '-tooltip-color')
        background-color lookup(theme + '-tooltip-bg')
</style>



