import Notification from './notification'
import Vue from 'vue'
import camelcaseToHyphen from '../../../utils/string/camel-case-to-hyphen'
import { CLASS_PREFIX } from '../../../config'
// import store from 'store'

Notification.newInstance = properties => {
  const _props = properties || {}

  let props = ''
  Object.keys(_props).forEach(prop => {
    props += ` :${camelcaseToHyphen(prop)}=${prop}`
  })

  const el = document.createElement('div')
  el.innerHTML = `<notification${props}></notification>`
  document.body.appendChild(el)

  const notification = new Vue({
    el: el,
    // store,
    data: _props,
    components: { Notification }
  }).$children[0]

  return {
    notice (noticeProps) {
      notification.add(noticeProps)
    },
    remove (name) {
      notification.close(name)
    },
    component: notification,
    destroy () {
      notification.closeAll()
      setTimeout(() => {
        document.body.removeChild(document.getElementsByClassName(`${CLASS_PREFIX}-message`)[0].parentElement)
      }, 500)
    }
  }
}

export default Notification



