<template>
  <div
    v-click-outside="onClickOutside"
    class="x-dropdown"
    @mouseenter="onMouseenter"
    @mouseleave="onMouseleave">
    <div
      ref="reference"
      class="x-dropdown__rel"
      @click="onRefClick">
      <slot></slot>
    </div>
    <transition :name="transition">
      <drop
        v-show="currentVisible"
        ref="drop"
        :placement="placement">
        <slot name="content"></slot>
      </drop>
    </transition>
  </div>
</template>

<script>
import Drop from '../select/dropdown'
import oneOf from '../../utils/collection/one-of'
import clickOutside from '../../directives/click-outside'

export default {
  name: 'Dropdown',

  components: {
    Drop
  },

  directives: {
    clickOutside
  },

  props: {
    // 触发方式
    trigger: {
      validator (value) {
        return oneOf(value, ['click', 'hover', 'custom'])
      },
      default: 'hover'
    },
    // 下拉框出现的位置
    placement: {
      validator (value) {
        return oneOf(value, ['top', 'top-start', 'top-end', 'bottom', 'bottom-start', 'bottom-end', 'left', 'left-start', 'left-end', 'right', 'right-start', 'right-end'])
      },
      default: 'bottom'
    },
    // 是否可见
    visible: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      currentVisible: this.visible
    }
  },

  computed: {
    transition () {
      return ['bottom-start', 'bottom', 'bottom-end'].indexOf(this.placement) > -1 ? 'slide-up' : 'fade'
    }
  },

  watch: {
    visible (val) {
      this.currentVisible = val
    },
    currentVisible (val) {
      if (val) {
        this.$refs.drop.updatePopper()
      } else {
        this.$refs.drop.destroyPopper()
      }
      this.$emit('toggle-visible', val)
    }
  },

  mounted () {
    this.$on('click', (key) => {
      const $parent = this.hasParent()
      if ($parent) $parent.$emit('click', key)
    })

    this.$on('hover-click', () => {
      const $parent = this.hasParent()
      if ($parent) {
        this.$nextTick(() => {
          if (this.trigger === 'custom') return false
          this.currentVisible = false
        })
        $parent.$emit('hover-click')
      } else {
        this.$nextTick(() => {
          if (this.trigger === 'custom') return false
          this.currentVisible = false
        })
      }
    })

    this.$on('haschild-click', () => {
      this.$nextTick(() => {
        if (this.trigger === 'custom') return false
        this.currentVisible = true
      })
      const $parent = this.hasParent()
      if ($parent) $parent.$emit('haschild-click')
    })
  },

  methods: {
    /**
     * 处理组件外点击
     */
    onClickOutside () {
      if (this.trigger === 'custom' || this.trigger !== 'click') {
        return false
      }
      this.currentVisible = false
    },

    /**
     * 处理鼠标移入
     */
    onMouseenter () {
      if (this.trigger === 'custom' || this.trigger !== 'hover') {
        return false
      }
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        this.currentVisible = true
      }, 250)
    },

    /**
     * 处理鼠标移出
     */
    onMouseleave () {
      if (this.trigger === 'custom' || this.trigger !== 'hover') {
        return false
      }
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        this.currentVisible = false
      }, 150)
    },

    /**
     * 处理触发元素点击
     */
    onRefClick () {
      if (this.trigger === 'custom' || this.trigger !== 'click') {
        return false
      }
      this.currentVisible = !this.currentVisible
    },

    /**
     * 是否嵌套在组件内
     * @return Boolean
     */
    hasParent () {
      const $parent = this.$parent.$parent
      if ($parent && $parent.$options.name === 'Dropdown') {
        return $parent
      } else {
        return false
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/select'

.x-dropdown
  display inline-block

  .x-select-dropdown
    width auto
    overflow visible
    max-height none

  .x-dropdown
    width 100%

  &__rel
    display inline-block
    position relative

  &__menu
    min-width 80px

select-item(x-dropdown, x-dropdown-item)

for theme in themes
  .theme-{theme}
    select-item-color(theme, 'x-dropdown', 'x-dropdown-item')
</style>



