<template>
  <i :class="classes" :style="styles"></i>
</template>

<script>
import './font-awesome/style.css'

export default {
  name: 'Icon',

  props: {
    // 类型
    type: String,
    // 尺寸
    size: [Number, String],
    // 颜色
    color: String
  },

  computed: {
    classes () {
      return `icon icon-${this.type}`
    },
    styles () {
      let style = {}

      if (this.size) {
        style['font-size'] = `${this.size}px`
      }

      if (this.color) {
        style.color = this.color
      }

      return style
    }
  }
}
</script>



