/**
 * https://github.com/freeze-component/vue-popper
 *
 */
import PopperUtils from 'popper.js/dist/umd/popper-utils'
import Popper from 'popper.js'

export default {
  props: {
    // 参照
    reference: Object,
    // 弹出框
    popper: Object,
    // 弹出框相对参照的位置
    placement: {
      type: String,
      default: 'bottom'
    },
    // 偏移
    offset: {
      default: 0
    },
    value: Boolean,
    transition: String,
    // 弹出框小箭头箭头是否可见
    arrowVisible: Boolean,
    // 弹出框是否插入到 body
    appendToBody: {
      type: Boolean,
      default: true
    },
    // 弹出选项
    // https://github.com/FezVrasta/popper.js/blob/master/docs/_includes/popper-documentation.md
    // https://github.com/FezVrasta/popper.js/issues/62
    options: {
      type: Object,
      default () {
        return {
          gpuAcceleration: false, // 是否启用 GPU 加速渲染
          modifiers: {
            offset: {
              offset: 0
            }
            // flip: {
            //   boundariesElement: 'window' // 弹出框溢出边界 ['scrollParent'|'window'|'viewport'|任意的DOM]
            // }
          }
        }
      }
    }
  },

  data () {
    return {
      visible: this.value // 弹出框是否可见
    }
  },

  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.visible = val
        this.$emit('input', val)
      }
    },
    visible (val) {
      if (val) {
        this.updatePopper()
      } else {
        this.destroyPopper()
        this.$emit('hide-popper')
      }
      this.$emit('input', val)
    }
  },

  beforeDestroy () {
    this.popperJS && this.popperJS.destroy()
    this.popperElm && this.popperElm.parentNode === document.body && document.body.removeChild(this.popperElm)
  },

  methods: {
    /**
     * 创建弹出框
     */
    createPopper () {
      if (!/^(top|bottom|left|right)(-start|-end)?$/g.test(this.placement)) return

      const options = this.options
      const popper = this.popperElm = this.popperElm || this.popper || this.$refs.popper
      const reference = this.referenceElm = this.referenceElm || this.reference || this.$refs.reference
      // const popper = this.popper || this.$refs.popper
      // const reference = this.reference || this.$refs.reference

      // 弹出框和参照都必须存在
      if (!popper || !reference) return

      if (this.arrowVisible) this.appendArrow(popper)
      if (this.appendToBody) document.body.appendChild(this.popperElm)

      // 先销毁之前存在的弹出实例
      if (this.popperJS && this.popperJs.hasOwnProperty('destroy')) {
        this.popperJS.destroy()
      }

      options.placement = this.placement
      options.modifiers.offset.offset = this.offset
      options.onCreate = popper => {
        this.$emit('created', this)
      }

      // 弹出实例
      this.popperJS = new Popper(reference, popper, options)
    },

    /**
     * 更新弹出信息框
     */
    updatePopper () {
      this.popperJS ? this.popperJS.update() : this.createPopper()
    },

    /**
     * 销毁弹出信息框
     */
    destroyPopper () {
      this.popperJS && this.resetTransformOrigin(this.popperJS)
    },

    /**
     * 添加小箭头
     * @param {HTML DOM Element} el 需要添加小箭头的元素
     */
    appendArrow (el) {
      let hash
      if (this.appended) return

      this.appended = true

      for (let item in el.attributes) {
        if (/^_v-/.test(el.attributes[item].name)) {
          hash = el.attributes[item].name
          break
        }
      }

      const arrow = document.createElement('div')

      if (hash) arrow.setAttribute(hash, '')
      arrow.setAttribute(`x-arrow`, '')
      arrow.className = `x-popper__arrow`
      el.appendChild(arrow)
    },

    /**
     * 重置CSS3动画原点
     * @param {Object} popper 弹出实例
     */
    resetTransformOrigin (popper) {
      let placementMap = {top: 'bottom', bottom: 'top', left: 'right', right: 'left'}
      let placement = popper.options.placement.split('-')[0]
      let origin = placementMap[placement]
      let transformOrigin = ['top', 'bottom'].indexOf(placement) > -1 ? `center ${origin}` : `${origin} center`
      PopperUtils.setStyles(popper.popper, { transformOrigin })
    }
  }
}



