<template>
  <div :class="classes" :style="rowStyle">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'XRow',

  props: {
    // 栅格间隔
    gutter: {
      type: Number,
      default: 0
    }
  },

  computed: {
    // 类
    classes () {
      return [
        `x-row`
      ]
    },

    // 样式
    rowStyle () {
      return {
        marginLeft: `-${this.gutter}px`,
        marginRight: `-${this.gutter}px`
      }
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-row
  position relative
  /*width 100%*/
  clearfix()
</style>



