<template>
  <div class="x-loading-bar-root">
    <div
      v-if="percent > 0"
      class="x-loading-bar"
      :style="barStyle">
    </div>
  </div>
</template>

<script>
export default {
  name: 'LoadingBar',

  // props: {
  //   color: {
  //     type: String,
  //     default: 'green'
  //   },
  //   percent: {
  //     type: Number,
  //     default: 70
  //   },
  //   duration: 200
  // },

  data () {
    return {
      color: 'green',
      percent: 0,
      duration: 200,

      // styleWidth是私有属性不能设置
      styleWidth: 0
    }
  },

  computed: {
    barStyle () {
      let result = {
        backgroundColor: this.color,
        width: this.styleWidth + '%',
        transitionDuration: this.duration / 1000 + 's'
      }
      return result
    }
  },

  watch: {
    percent (val) {
      clearTimeout(this.tid1)
      clearTimeout(this.tid2)
      this.tid1 = setTimeout(() => {
        this.styleWidth = val
      }, 10)
      if (val - 0 === 100) {
        this.tid2 = setTimeout(() => {
          this.percent = 0
        }, this.duration - 0 + 100)
      }
    }
  },

  methods: {
    run (options = 0) {
      if (typeof options === 'number') {
        options = {
          percent: options
        }
      }
      Object.keys(options).forEach((key) => {
        this[key] = options[key]
      })
    },

    start () {
      this.run()
    },

    end () {
      this.run(100)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../styles/common'

  .x-loading-bar-root
    position fixed
    top 0
    left 0
    width 100%
    height 2px
    z-index zindex-loading-bar
    .x-loading-bar
      height 2px
      box-shadow 1px 1px 2px 2px rgba(0,0,0,0.06)
      transition all ease 0.2s

</style>



