<template>
  <div @click="draw" class="x-captcha">
    <canvas ref="canvas" :width="width" :height="height" :style="canvasStyle"></canvas>
  </div>
</template>

<script>
export default {
  name: 'Captcha',

  props: {
    // 值
    value: {
      type: String,
      default: ''
    },
    // 宽度
    width: {
      type: Number,
      default: 100
    },
    // 高度
    height: {
      type: Number,
      default: 40
    },
    // 背景色
    background: {
      type: String,
      default: '#DDD'
    },
    // 前景色
    foreground: {
      type: String,
      default: '#333'
    }
  },

  data () {
    return {
    }
  },

  computed: {
    canvasStyle () {
      return {
        width: `${this.width}px`,
        height: `${this.height}px`
      }
    }
  },

  mounted () {
    this.$nextTick(() => {
      this.draw()
    })
  },

  methods: {
    /**
     * 描绘验证码
     */
    draw () {
      let context = this.$refs.canvas.getContext('2d')
      let str = this.genRandomText()
      this.$emit('input', str)

      context.fillStyle = this.background
      context.fillRect(0, 0, this.width, this.height)

      context.font = 'bold 20px "Comic Sans MS", cursive, sans-serif'
      context.textBaseline = 'middle'
      let w = context.measureText(str).width
      context.fillStyle = this.foreground
      context.fillText(str, (this.width - w) / 2, this.height / 2)
    },

    /**
     * 生成随机字符串
     */
    genRandomText () {
      let arr = []
      let possibleCharacters = 'ABCDEFGHIJKLMNPQRSTUVWXYZabcdefghijklmnpqrstuvwxyz123456789'
      let length = Math.floor((Math.random() * 3) + 4)
      for (let i = 0; i < length; i++) {
        arr.push(possibleCharacters.charAt(Math.floor(Math.random() * possibleCharacters.length)))
      }
      return arr.join('')
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-captcha
  overflow hidden
  height 100px
  user-select none
</style>



