<template>
  <div :class="classes" v-click-outside="onClose">
    <div
      :class="[baseClass + '__trigger']"
      ref="reference"
      @click="toggleDropdown">
      <div class="x-select-tag" v-for="(item, index) in selectedMultiple">
        <span class="x-select-tag__text">{{ item.label }}</span>
        <icon type="close" @click.native.stop="removeTag(index)"></icon>
      </div>
      <span :class="[baseClass + '__placeholder']" v-show="showPlaceholder && !filterable">{{ localePlaceholder }}</span>
      <span :class="[baseClass + '__selected-value']" v-show="!showPlaceholder && !multiple && !filterable">{{ selectedSingle }}</span>
      <input
        type="text"
        v-if="filterable"
        v-model="query"
        :class="[baseClass + '__input']"
        :placeholder="showPlaceholder ? localePlaceholder : ''"
        :style="inputStyle"
        @blur="onBlur"
        @keydown="resetInputState"
        @keydown.delete="onInputDelete"
        ref="input">
      <icon type="times-circle" :class="[baseClass + '__icon']" v-show="showCloseIcon" @click.native.stop="clearSingleSelect"></icon>
      <icon type="caret-down" :class="[baseClass + '__icon']"></icon>
    </div>
    <transition name="slide-up">
      <drop v-show="visible" ref="dropdown">
        <ul v-show="notFound" :class="[baseClass + '__not-found']"><li>{{ localeNotFoundText }}</li></ul>
        <ul v-show="!notFound" :class="[baseClass + '__dropdown-list']" ref="options"><slot></slot></ul>
      </drop>
    </transition>
  </div>
</template>
<script>
import Drop from './dropdown'
import Icon from '../icon/icon'
import oneOf from '../../utils/collection/one-of'
import Emitter from '../../mixins/emitter'
import clickOutside from '../../directives/click-outside'

const baseClass = `x-select`

export default {
  name: 'XSelect',

  components: {
    Drop,
    Icon
  },

  directives: {
    clickOutside
  },

  mixins: [ Emitter ],

  props: {
    // 值
    value: {
      type: [String, Number, Array],
      default: ''
    },
    // 是否多选模式
    multiple: {
      type: Boolean,
      default: false
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 是否允许清除
    clearable: {
      type: Boolean,
      default: false
    },
    // 占位符
    placeholder: {
      type: String
    },
    // 是否根据输入的值筛选待选项
    filterable: {
      type: Boolean,
      default: false
    },
    // 筛选方法
    filterMethod: {
      type: Function
    },
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large', 'default'])
      }
    },
    // 在返回选项时，是否将 label 和 value 一并返回，默认只返回 value
    labelInValue: {
      type: Boolean,
      default: false
    },
    // 当下拉列表为空时显示的内容
    notFoundText: {
      type: String
    }
  },

  data () {
    return {
      baseClass: baseClass,
      visible: false,
      options: [],
      optionInstances: [],
      selectedSingle: '',  // 标签
      selectedMultiple: [],
      focusIndex: 0,
      query: '',
      inputLength: 20,
      notFound: false,
      slotChangeDuration: false,  // 多选模式下并且插槽发生改变，此值设为 true，插槽改变后设置为 false
      model: this.value
    }
  },
  computed: {
    classes () {
      return [
        `${baseClass}`,
        {
          [`${baseClass}--visible`]: this.visible,
          [`${baseClass}--disabled`]: this.disabled,
          [`${baseClass}--multiple`]: this.multiple,
          [`${baseClass}--single`]: !this.multiple,
          [`${baseClass}--show-clear`]: this.showCloseIcon,
          [`${baseClass}--${this.size}`]: !!this.size
        }
      ]
    },
    showPlaceholder () {
      let status = false
      if ((typeof this.model) === 'string') {
        if (this.model === '') {
          status = true
        }
      } else if (Array.isArray(this.model)) {
        if (!this.model.length) {
          status = true
        }
      }
      return status
    },
    showCloseIcon () {
      return !this.multiple && this.clearable && !this.showPlaceholder
    },
    inputStyle () {
      let style = {}
      if (this.multiple) {
        if (this.showPlaceholder) {
          style.width = '100%'
        } else {
          style.width = `${this.inputLength}px`
        }
      }
      return style
    },
    localePlaceholder () {
      if (this.placeholder === undefined) {
        // TODO: 多语言
        return '请选择'
      } else {
        return this.placeholder
      }
    },
    localeNotFoundText () {
      if (this.notFoundText === undefined) {
        // TODO: 多语言
        return '暂无匹配数据'
      } else {
        return this.notFoundText
      }
    }
  },

  mounted () {
    this.modelToQuery()
    this.updateOptions(true)
    document.addEventListener('keydown', this.onKeydown)
    this.$on('append', () => {
      this.modelToQuery()
      this.slotChange()
      this.updateOptions(true, true)
    })
    this.$on('remove', () => {
      this.modelToQuery()
      this.slotChange()
      this.updateOptions(true, true)
    })
    this.$on('select-option', (value) => {
      if (this.model === value) {
        this.hideDropdown()
      } else {
        if (this.multiple) {
          const index = this.model.indexOf(value)
          if (index >= 0) {
            this.removeTag(index)
          } else {
            this.model.push(value)
            this.broadcast('Drop', 'update-popper')
          }
          if (this.filterable) {
            this.query = ''
            this.$refs.input.focus()
          }
        } else {
          this.model = value
          if (this.filterable) {
            this.findChild((child) => {
              if (child.value === value) {
                this.query = child.label === undefined ? child.searchLabel : child.label
              }
            })
          }
        }
      }
    })
  },
  beforeDestroy () {
    document.removeEventListener('keydown', this.onKeydown)
  },
  watch: {
    value (val) {
      this.model = val
      if (val === '') this.query = ''
    },
    model () {
      this.$emit('input', this.model)
      this.modelToQuery()
      if (this.multiple) {
        if (this.slotChangeDuration) {
          this.slotChangeDuration = false
        } else {
          this.updateMultipleSelected()
        }
      } else {
        this.updateSingleSelected()
      }
    },
    visible (val) {
      if (val) {
        if (this.filterable) {
          if (this.multiple) {
            this.$refs.input.focus()
          } else {
            this.$refs.input.select()
          }
        }
        this.broadcast('Drop', 'update-popper')
      } else {
        if (this.filterable) {
          this.$refs.input.blur()
          // #566 reset options visible
          setTimeout(() => {
            this.broadcastQuery('')
          }, 300)
        }
        this.broadcast('Drop', 'destroy-popper')
      }
    },
    query (val) {
      this.$emit('on-query-change', val)
      this.broadcastQuery(val)

      let isHidden = true
      this.$nextTick(() => {
        this.findChild((child) => {
          if (!child.hidden) {
            isHidden = false
          }
        })
        this.notFound = isHidden
      })
      this.broadcast('Drop', 'update-popper')
    }
  },

  methods: {
    /**
     * 切换下拉选项的显示状态
     */
    toggleDropdown () {
      if (this.disabled) {
        return false
      }
      this.visible = !this.visible
    },

    /**
     * 隐藏下拉选项
     */
    hideDropdown () {
      this.visible = false
      this.focusIndex = 0
      // 向选项组件广播关闭事件
      this.broadcast('XOption', 'close-select')
    },

    /**
     * 查找选项组件
     * @param  {Function} cb 条件
     */
    findChild (cb) {
      const find = function (child) {
        const name = child.$options.componentName
        if (name) {
          cb(child)
        } else if (child.$children.length) {
          child.$children.forEach((innerChild) => {
            find(innerChild, cb)
          })
        }
      }
      if (this.optionInstances.length) {
        this.optionInstances.forEach((child) => {
          find(child)
        })
      } else {
        this.$children.forEach((child) => {
          find(child)
        })
      }
    },
    updateOptions (init, slot = false) {
      let options = []
      let index = 1
      this.findChild((child) => {
        options.push({
          value: child.value,
          label: (child.$el.getAttribute('option-label') === undefined) ? child.label : child.$el.getAttribute('option-label')
        })
        child.index = index++
        if (init) {
          this.optionInstances.push(child)
        }
      })
      this.options = options
      if (init) {
        this.updateSingleSelected(true, slot)
        this.updateMultipleSelected(true, slot)
      }
    },
    updateSingleSelected (init = false, slot = false) {
      const type = typeof this.model
      if (type === 'string' || type === 'number') {
        let findModel = false
        for (let i = 0; i < this.options.length; i++) {
          if (this.model === this.options[i].value) {
            this.selectedSingle = this.options[i].label
            findModel = true
            break
          }
        }
        if (slot && !findModel) {
          this.model = ''
          this.query = ''
        }
      }
      this.toggleSingleSelected(this.model, init)
    },
    clearSingleSelect () {
      if (this.showCloseIcon) {
        this.findChild((child) => {
          child.selected = false
        })
        this.model = ''
        if (this.filterable) {
          this.query = ''
        }
      }
    },
    updateMultipleSelected (init = false, slot = false) {
      if (this.multiple && Array.isArray(this.model)) {
        let selected = []
        for (let i = 0; i < this.model.length; i++) {
          const model = this.model[i]
          for (let j = 0; j < this.options.length; j++) {
            const option = this.options[j]
            if (model === option.value) {
              selected.push({
                value: option.value,
                label: option.label
              })
            }
          }
        }
        this.selectedMultiple = selected
        if (slot) {
          let selectedModel = []
          for (let i = 0; i < selected.length; i++) {
            selectedModel.push(selected[i].value)
          }
          // 通知使用者插槽发生了改变并且移除了一个已选项
          if (this.model.length === selectedModel.length) {
            this.slotChangeDuration = true
          }
          this.model = selectedModel
        }
      }
      this.toggleMultipleSelected(this.model, init)
    },
    removeTag (index) {
      if (this.disabled) {
        return false
      }
      this.model.splice(index, 1)
      if (this.filterable && this.visible) {
        this.$refs.input.focus()
      }
      this.broadcast('Drop', 'update-popper')
    },

    /**
     * 处理单选模式的选择
     * @param  {String}  value        值
     * @param  {Boolean} [init=false] 是否初始化
     */
    toggleSingleSelected (value, init = false) {
      if (!this.multiple) {
        let label = ''
        this.findChild((child) => {
          if (child.value === value) {
            child.selected = true
            label = (child.label === undefined) ? child.$el.innerHTML : child.label
          } else {
            child.selected = false
          }
        })
        this.hideDropdown()
        if (!init) {
          if (this.labelInValue) {
            this.$emit('change', {
              value: value,
              label: label
            })
            this.dispatch('XFormItem', 'fieldChange', {
              value: value,
              label: label
            })
          } else {
            this.$emit('change', value)
            this.dispatch('XFormItem', 'fieldChange', value)
          }
        }
      }
    },

    /**
     * 处理多选模式的选择
     * @param  {String}  value        值
     * @param  {Boolean} [init=false] 是否初始化
     */
    toggleMultipleSelected (value, init = false) {
      if (this.multiple) {
        let hybridValue = []
        for (let i = 0; i < value.length; i++) {
          hybridValue.push({
            value: value[i]
          })
        }
        this.findChild((child) => {
          const index = value.indexOf(child.value)
          if (index >= 0) {
            child.selected = true
            hybridValue[index].label = (child.label === undefined) ? child.$el.innerHTML : child.label
          } else {
            child.selected = false
          }
        })
        if (!init) {
          if (this.labelInValue) {
            this.$emit('on-change', hybridValue)
            this.dispatch('XFormItem', 'fieldChange', hybridValue)
          } else {
            this.$emit('on-change', value)
            this.dispatch('XFormItem', 'fieldChange', value)
          }
        }
      }
    },

    /**
     * 处理下拉关闭
     */
    onClose () {
      this.hideDropdown()
    },

    /**
     * 处理按钮按下
     * @param  {HTML DOM Event} e 事件
     */
    onKeydown (e) {
      if (this.visible) {
        const keyCode = e.keyCode
        // Esc
        if (keyCode === 27) {
          e.preventDefault()
          this.hideDropdown()
        }
        // 下箭头键
        if (keyCode === 40) {
          e.preventDefault()
          this.navigateOptions('next')
        }
        // 上箭头键
        if (keyCode === 38) {
          e.preventDefault()
          this.navigateOptions('prev')
        }
        // 回车
        if (keyCode === 13) {
          e.preventDefault()
          this.findChild((child) => {
            if (child.isFocus) {
              child.select()
            }
          })
        }
      }
    },
    navigateOptions (direction) {
      if (direction === 'next') {
        const next = this.focusIndex + 1
        this.focusIndex = (this.focusIndex === this.options.length) ? 1 : next
      } else if (direction === 'prev') {
        const prev = this.focusIndex - 1
        this.focusIndex = (this.focusIndex <= 1) ? this.options.length : prev
      }
      let chilidStatus = {
        disabled: false,
        hidden: false
      }
      let findDeep = false  // 能否继续查找下一个
      this.findChild((child) => {
        if (child.index === this.focusIndex) {
          chilidStatus.disabled = child.disabled
          chilidStatus.hidden = child.hidden
          if (!child.disabled && !child.hidden) {
            child.isFocus = true
          }
        } else {
          child.isFocus = false
        }
        if (!child.hidden && !child.disabled) {
          findDeep = true
        }
      })
      this.resetScrollTop()
      if ((chilidStatus.disabled || chilidStatus.hidden) && findDeep) {
        this.navigateOptions(direction)
      }
    },
    resetScrollTop () {
      const index = this.focusIndex - 1
      let bottomOverflowDistance = this.optionInstances[index].$el.getBoundingClientRect().bottom - this.$refs.dropdown.$el.getBoundingClientRect().bottom
      let topOverflowDistance = this.optionInstances[index].$el.getBoundingClientRect().top - this.$refs.dropdown.$el.getBoundingClientRect().top
      if (bottomOverflowDistance > 0) {
        this.$refs.dropdown.$el.scrollTop += bottomOverflowDistance
      }
      if (topOverflowDistance < 0) {
        this.$refs.dropdown.$el.scrollTop += topOverflowDistance
      }
    },
    onBlur () {
      setTimeout(() => {
        const model = this.model
        if (this.multiple) {
          this.query = ''
        } else {
          if (model !== '') {
            this.findChild((child) => {
              if (child.value === model) {
                this.query = child.label === undefined ? child.searchLabel : child.label
              }
            })
          } else {
            this.query = ''
          }
        }
      }, 300)
    },
    resetInputState () {
      this.inputLength = this.$refs.input.value.length * 12 + 20
    },
    onInputDelete () {
      if (this.multiple && this.model.length && this.query === '') {
        this.removeTag(this.model.length - 1)
      }
    },

    /**
     * 处理 slot 更改
     */
    slotChange () {
      this.options = []
      this.optionInstances = []
    },

    /**
     * 设置搜索词
     */
    setQuery (query) {
      if (!this.filterable) return
      this.query = query
    },
    modelToQuery () {
      if (!this.multiple && this.filterable && this.model) {
        this.findChild((child) => {
          if (this.model === child.value) {
            if (child.label) {
              this.query = child.label
            } else if (child.searchLabel) {
              this.query = child.searchLabel
            } else {
              this.query = child.value
            }
          }
        })
      }
    },
    broadcastQuery (val) {
      this.broadcast('XOption', 'change-query', val)
    }
  }
}
</script>


<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/inner-icon'
@import '../../styles/mixins/placeholder'

for theme in themes
  .theme-{theme}
    .x-select
      color lookup(theme + '-select-color')

      &__trigger
        background-color lookup(theme + '-select-bg')
        border 1px solid lookup(theme + '-select-border-color')

      &:hover
        border-color lookup(theme + '-select-hover-border-color')

      &__icon
        color lookup(theme + '-select-icon-color')

      &--visible
        .x-select__trigger
          border-color lookup(theme + '-select-active-border-color')

      &--disabled
        .x-select__trigger
          background-color lookup(theme + '-select-disabled-bg')
          color lookup(theme + '-select-disabled-color')
          &:hover
            border-color lookup(theme + '-select-disabled-hover-border-color')

      &--single,
      &--multiple
        .x-select__placeholder
          color lookup(theme + '-select-placeholder-color')

      // 输入框
      &__input
        color lookup(theme + '-select-color')
        background-color lookup(theme + '-select-bg')
        placeholder(lookup(theme + '-select-placeholder-color'))

    .x-select-tag
      border 1px solid lookup(theme + '-select-tag-border-color')
      background lookup(theme + '-select-tag-bg')

    .x-select-tag .icon
        color lookup(theme + '-select-icon-color')

// 下拉选择框
.x-select
  display inline-block
  width 100%
  box-sizing border-box
  vertical-align middle
  font-size font-size-base
  line-height normal

  &__trigger
    display block
    box-sizing border-box
    outline none
    user-select none
    cursor pointer
    position relative
    border-radius border-radius-base
    transition all transition-time ease-in-out

    .x-select__icon:nth-of-type(1)
      display none
      cursor pointer

    &:hover
      .x-select__icon:nth-of-type(1)
        display inline-block

  &--show-clear &__trigger:hover .x-select__icon:nth-of-type(2)
    display none

  &__icon
    inner-icon()

  &--visible
    .x-select__trigger
      outline 0
    .x-select__icon:nth-of-type(2)
      transform rotate(180deg)

  &--disabled
    .x-select__trigger
      opacity 1
      cursor not-allowed

      .x-select__icon:nth-of-type(1)
        display none

      &:hover
        box-shadow none

        .x-select__icon:nth-of-type(2)
          display inline-block

  &--single &__trigger
    height input-height-base
    position relative

    .x-select__placeholder, .x-select__selected-value
      display block
      height input-height-base - 2px
      line-height input-height-base - 2px
      font-size font-size-small
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      padding-left 8px
      padding-right 24px

  &--large&--single &__trigger
    height input-height-large

    .x-select__placeholder, .x-select__selected-value
      height input-height-large - 2px
      line-height input-height-large - 2px
      font-size font-size-base

  &--small&--single &__trigger
    height input-height-small
    border-radius btn-border-radius-small

    .x-select__placeholder, .x-select__selected-value
      height input-height-small - 2px
      line-height input-height-small - 2px

  &--multiple &__trigger
    padding 0 24px 0 4px
    min-height input-height-base

    .x-select__placeholder
      display block
      height input-height-base - 2px
      line-height input-height-base - 2px
      font-size font-size-small
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      padding-left 4px
      padding-right 22px

  // 输入框
  &__input
    display inline-block
    height input-height-base - 2
    line-height input-height-base
    padding 0 24px 0 8px
    font-size font-size-small
    outline none
    border none
    box-sizing border-box
    position relative
    cursor pointer

  &--single &__input
    width 100%

  &--large &__input
    font-size font-size-base
    height input-height-large

  &--small &__input
    height input-height-small

  &--multiple &__input
    height input-height-base - 3px
    line-height input-height-base
    padding 0 0 0 4px

  &__not-found
    text-align center
    color btn-disable-color

  &--multiple .{css-prefix}-tag
    margin 3px 4px 2px 0

// 标签
.x-select-tag
  display inline-block
  height 22px
  line-height 22px
  margin 2px 4px 2px 0
  padding 0 8px
  border-radius btn-border-radius-small
  font-size font-size-small
  vertical-align middle
  opacity 1
  overflow hidden
  cursor pointer
</style>



