let components = [
  'grid',
  'button',
  'confirm',
  'icon',
  'input',
  'select',
  'radio',
  'checkbox',
  'date-picker',
  'time-picker',
  'switch',
  'slider',
  'cascader',
  'captcha',
  'upload',
  'editor',
  'breadcrumb',
  'tabs',
  'pagination',
  'message',
  'notice',
  'alert',
  'tooltip',
  'modal',
  'progress',
  'dnd',
  'scroll-view',
  'dropdown',
  'tree',
  'spin',
  'loading-bar',
  'animate-text',
  'table',
  'validator',
  'search-box',
  'grid-layout',
  'form'
]
let children = [{ path: '', redirect: 'grid' }]

components.forEach((name) => {
  children.push({
    path: name,
    component: r => require.ensure([], () => r(require('docs/views/component/' + name)), 'component')
  })
})

export default [{
  path: '/component',
  component: r => require.ensure([], () => r(require('docs/views/component')), 'component'),
  children: children
}]



