<template>
  <div :class="wrapClasses" v-click-outside="onCancel">
    <div
      ref="reference"
      class="x-cascader__trigger"
      @click="toggleDropdown">
      <span class="x-cascader__placeholder" v-show="showPlaceholder">{{ localePlaceholder }}</span>
      <span class="x-cascader__selected-value" v-show="!showPlaceholder">{{ formattedLabel }}</span>
      <icon type="times-circle" class="x-cascader__icon" v-show="showCloseIcon"></icon>
      <icon type="caret-down" class="x-cascader__icon"></icon>
    </div>
    <transition name="slide-up">
      <drop v-show="visible" ref="dropdown">
        <cascader-panel
          :data="data"
          :value="currentValue"
          @valueChange="onValueChange">
        </cascader-panel>
      </drop>
    </transition>
  </div>
</template>

<script>
import Drop from '../select/dropdown'
import Icon from '../icon/icon'
import CascaderPanel from './cascader-panel'
import oneOf from '../../utils/collection/one-of'
import Emitter from '../../mixins/emitter'
import clickOutside from '../../directives/click-outside'

export default {
  name: 'Cascader',

  mixins: [Emitter],

  components: {
    Icon,
    Drop,
    CascaderPanel
  },

  directives: {
    clickOutside
  },

  props: {
    // 待选数据项
    data: {
      type: Array,
      default () {
        return []
      }
    },
    // 值
    value: {
      type: Array,
      default () {
        return []
      }
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 占位符
    placeholder: {
      type: String
    },
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large', 'default'])
      }
    },
    // 格式化函数
    formatter: {
      type: Function,
      default (labels) {
        return labels.join(' / ')
      }
    }
  },

  data () {
    return {
      currentValue: this.value,
      visible: false
    }
  },

  computed: {
    wrapClasses () {
      return [
        'x-cascader',
        {
          [`x-cascader--visible`]: this.visible,
          [`x-cascader--disabled`]: this.disabled,
          [`x-cascader--show-clear`]: this.showCloseIcon,
          [`x-cascader--${this.size}`]: !!this.size
        }
      ]
    },
    showPlaceholder () {
      let status = false
      if (!this.currentValue.length) {
        status = true
      }
      return status
    },
    // 是否显示关闭按钮
    showCloseIcon () {
      return this.clearable && !this.showPlaceholder
    },
    localePlaceholder () {
      if (this.placeholder === undefined) {
        // TODO: 多语言
        return '请选择'
      } else {
        return this.placeholder
      }
    },
    formattedLabel () {
      let labels = this.currentValue.map(item => item.label)
      return this.formatter(labels)
    }
  },

  watch: {
    visible (val) {
      if (val) {
        this.broadcast('Drop', 'update-popper')
      } else {
        this.broadcast('Drop', 'destroy-popper')
      }
    },
    value (val) {
      this.currentValue = val
    },
    currentValue (val) {
      this.$emit('input', val)
      this.dispatch('XFormItem', 'fieldChange', val)
    }
  },

  methods: {
    /**
     * 监听子组件value改变
     * value: 当前值
     * params 其他参数  索引和是否有下级菜单
     */
    onValueChange (value, params) {
      this.currentValue = value
      if (!params.hasChildren) {
        this.$emit('change', value)
        this.hideDropdown()
      }
    },
    /**
     * 取消事件
     */
    onCancel () {
      this.currentValue = this.value
      this.hideDropdown()
    },

    /**
     * 切换下拉选项的显示状态
     */
    toggleDropdown () {
      if (this.disabled) {
        return false
      }
      this.visible = !this.visible
    },

    /**
     * 隐藏下拉选项
     */
    hideDropdown () {
      this.visible = false
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/inner-icon'
@import '../../styles/mixins/placeholder'

for theme in themes
  .theme-{theme}
    .x-cascader
      color lookup(theme + '-cascader-color')

      &__trigger
        background-color lookup(theme + '-cascader-bg')
        border 1px solid lookup(theme + '-cascader-border-color')

        &:hover
        border-color lookup(theme + '-cascader-hover-border-color')

        .x-cascader__placeholder
          color lookup(theme + '-cascader-placeholder-color')

      &__icon
        color lookup(theme + '-cascader-icon-color')

.x-cascader
  display inline-block
  width 100%
  box-sizing border-box
  vertical-align middle
  font-size font-size-base
  line-height normal

  &__trigger
    height input-height-base
    display block
    box-sizing border-box
    outline none
    user-select none
    cursor pointer
    position relative
    border-radius border-radius-base
    transition all transition-time ease-in-out

    .x-cascader__icon:nth-of-type(1)
      display none
      cursor pointer

    &:hover
      .x-cascader__icon:nth-of-type(1)
        display inline-block

    .x-cascader__placeholder, .x-cascader__selected-value
      display block
      height input-height-base - 2px
      line-height input-height-base - 2px
      font-size font-size-small
      overflow hidden
      text-overflow ellipsis
      white-space nowrap
      padding-left 8px
      padding-right 24px

  &__icon
    inner-icon()

  &--large &__trigger
    height input-height-large

    .x-cascader__placeholder, .x-cascader__selected-value
      height input-height-large - 2px
      line-height input-height-large - 2px
      font-size font-size-base

  &--small &__trigger
    height input-height-small
    border-radius btn-border-radius-small

    .x-cascader__placeholder, .x-cascader__selected-value
      height input-height-small - 2px
      line-height input-height-small - 2px

  .x-select-dropdown
    width auto
    padding 0
    white-space nowrap
    overflow visible
</style>



