<template>
  <div :class="classes">
    <div :class="[prefixCls + '-sidebar']" v-if="shortcuts.length">
      <div
        :class="[prefixCls + '-shortcut']"
        v-for="shortcut in shortcuts"
        @click="handleShortcutClick(shortcut)">{{ shortcut.text }}</div>
    </div>
    <div :class="[prefixCls + '-body']">
      <div :class="[datePrefixCls + '-header']" v-show="currentView !== 'time'">
        <span
          :class="iconBtnCls('prev', '-double')"
          @click="prevYear"><icon type="angle-double-left"></icon></span>
        <span
          :class="iconBtnCls('prev')"
          @click="prevMonth"
          v-show="currentView === 'date'"><icon type="angle-left"></icon></span>
        <span
          :class="[datePrefixCls + '-header-label']"
          @click="showYearPicker">{{ yearLabel }}</span>
        <span
          :class="[datePrefixCls + '-header-label']"
          @click="showMonthPicker"
          v-show="currentView === 'date'">{{ monthLabel }}</span>
        <span
          :class="iconBtnCls('next', '-double')"
          @click="nextYear"><icon type="angle-double-right"></icon></span>
        <span
          :class="iconBtnCls('next')"
          @click="nextMonth"
          v-show="currentView === 'date'"><icon type="angle-right"></icon></span>
      </div>
      <div :class="[prefixCls + '-content']">
        <date-table
          v-show="currentView === 'date'"
          :year="year"
          :month="month"
          :date="date"
          :value="value"
          :selection-mode="selectionMode"
          :disabled-date="disabledDate"
          @on-pick="handleDatePick"
          @on-pick-click="handlePickClick"></date-table>
        <year-table
          ref="year-table"
          v-show="currentView === 'year'"
          :year="year"
          :date="date"
          :selection-mode="selectionMode"
          :disabled-date="disabledDate"
          @on-pick="handleYearPick"
          @on-pick-click="handlePickClick"></year-table>
        <month-table
          ref="month-table"
          v-show="currentView === 'month'"
          :month="month"
          :date="date"
          :selection-mode="selectionMode"
          :disabled-date="disabledDate"
          @on-pick="handleMonthPick"
          @on-pick-click="handlePickClick"></month-table>
        <time-picker
          ref="time-picker"
          show-date
          v-show="currentView === 'time'"
          @on-pick="handleTimePick"
          @on-pick-click="handlePickClick"></time-picker>
      </div>
      <confirm
        v-if="confirm"
        :show-time="showTime"
        :is-time="isTime"
        @on-pick-toggle-time="handleToggleTime"
        @on-pick-clear="handlePickClear"
        @on-pick-success="handlePickSuccess"></confirm>
    </div>
  </div>
</template>
<script>
  import DateTable from '../base/date-table'
  import YearTable from '../base/year-table'
  import MonthTable from '../base/month-table'
  import TimePicker from './time'
  import Confirm from '../base/confirm'
  // import _ from 'lodash'

  import Mixin from './mixin'

  import { initTimeDate } from '../util'

  const prefixCls = 'x-picker-panel'
  const datePrefixCls = 'x-date-picker'

  export default {
    name: 'DatePicker',
    mixins: [ Mixin ],
    components: { DateTable, YearTable, MonthTable, TimePicker, Confirm },
    data () {
      return {
        prefixCls: prefixCls,
        datePrefixCls: datePrefixCls,
        shortcuts: [],
        currentView: 'date',
        date: initTimeDate(),
        value: '',
        showTime: false,
        selectionMode: 'day',
        disabledDate: '',
        year: null,
        month: null,
        confirm: false,
        isTime: false,
        format: 'yyyy-MM-dd'
      }
    },
    computed: {
      classes () {
        return [
          `${prefixCls}-body-wrapper`,
          {
            [`${prefixCls}--with-sidebar`]: this.shortcuts.length
          }
        ]
      },
      yearLabel () {
        const tYear = '年'
        const year = this.year
        if (!year) return ''
        if (this.currentView === 'year') {
          const startYear = Math.floor(year / 10) * 10
          return `${startYear}${tYear} - ${startYear + 9}${tYear}`
        }
        return `${year}${tYear}`
      },
      monthLabel () {
        const month = this.month + 1
        return `${month} 月`
      }
    },
    watch: {
      value (newVal) {
        if (!newVal) return
        newVal = new Date(newVal)
        if (!isNaN(newVal)) {
          this.date = newVal
          this.year = newVal.getFullYear()
          this.month = newVal.getMonth()
        }
        if (this.showTime) this.$refs['time-picker'].value = newVal
      },
      date (val) {
        if (this.showTime) this.$refs['time-picker'].date = val
      },
      format (val) {
        if (this.showTime) this.$refs['time-picker'].format = val
      },
      currentView (val) {
        if (val === 'time') {
          this.$refs['time-picker'].updateScroll()
        }
      }
    },
    methods: {
      resetDate () {
        this.date = new Date(this.date)
      },
      handleClear () {
        this.date = new Date()
        this.$emit('on-pick', '')
        if (this.showTime) this.$refs['time-picker'].handleClear()
      },
      resetView (reset = false) {
        if (this.currentView !== 'time' || reset) {
          if (this.selectionMode === 'month') {
            this.currentView = 'month'
          } else if (this.selectionMode === 'year') {
            this.currentView = 'year'
          } else {
            this.currentView = 'date'
          }
        }

        this.year = this.date.getFullYear()
        this.month = this.date.getMonth()
        if (reset) this.isTime = false
      },
      prevYear () {
        if (this.currentView === 'year') {
          this.$refs['year-table'].prevTenYear()
        } else {
          this.year--
          this.date.setFullYear(this.year)
          this.resetDate()
        }
      },
      nextYear () {
        if (this.currentView === 'year') {
          this.$refs['year-table'].nextTenYear()
        } else {
          this.year++
          this.date.setFullYear(this.year)
          this.resetDate()
        }
      },
      prevMonth () {
        this.month--
        if (this.month < 0) {
          this.month = 11
          this.year--
        }
      },
      nextMonth () {
        this.month++
        if (this.month > 11) {
          this.month = 0
          this.year++
        }
      },
      showYearPicker () {
        this.currentView = 'year'
      },
      showMonthPicker () {
        this.currentView = 'month'
      },
      handleToggleTime () {
        if (this.currentView === 'date') {
          this.currentView = 'time'
          this.isTime = true
        } else if (this.currentView === 'time') {
          this.currentView = 'date'
          this.isTime = false
        }
      },
      handleYearPick (year, close = true) {
        this.year = year
        if (!close) return

        this.date.setFullYear(year)
        if (this.selectionMode === 'year') {
          this.$emit('on-pick', new Date(year, 0, 1))
        } else {
          this.currentView = 'month'
        }

        this.resetDate()
      },
      handleMonthPick (month) {
        this.month = month
        const selectionMode = this.selectionMode
        if (selectionMode !== 'month') {
          this.date.setMonth(month)
          this.currentView = 'date'
          this.resetDate()
        } else {
          this.date.setMonth(month)
          this.year && this.date.setFullYear(this.year)
          this.resetDate()
          const value = new Date(this.date.getFullYear(), month, 1)
          this.$emit('on-pick', value)
        }
      },
      handleDatePick (value) {
        if (this.selectionMode === 'day') {
          this.$emit('on-pick', new Date(value.getTime()))
          this.date.setFullYear(value.getFullYear())
          this.date.setMonth(value.getMonth())
          this.date.setDate(value.getDate())
        }

        this.resetDate()
      },
      handleTimePick (date) {
        this.handleDatePick(date)
      }
    },
    mounted () {
      if (this.selectionMode === 'month') {
        this.currentView = 'month'
      }

      if (this.date && !this.year) {
        this.year = this.date.getFullYear()
        this.month = this.date.getMonth()
      }
      if (this.showTime) {
        // todo 这里可能有问题，并不能进入到这里，但不影响正常使用
        this.$refs['time-picker'].date = this.date
        this.$refs['time-picker'].value = this.value
        this.$refs['time-picker'].format = this.format
        this.$refs['time-picker'].showDate = true
      }
    }
  }
</script>



