<template>
  <div :class="classes" @click="handleClick">
    <span :class="getCellCls(cell)" v-for="(cell, cellIndex) in cells"><em :index="cellIndex">{{ cell.text }}</em></span>
  </div>
</template>

<script>
// import { deepCopy } from '../../../utils/assist'
import cloneDeep from 'lodash/cloneDeep'

const baseClass = 'x-date-picker-cells'

export default {
  props: {
    date: {},
    year: {},
    disabledDate: {},
    selectionMode: {
      default: 'year'
    }
  },
  computed: {
    classes () {
      return [
        `${baseClass}`,
        `${baseClass}--year`
      ]
    },
    startYear () {
      return Math.floor(this.year / 10) * 10
    },
    cells () {
      let cells = []
      const cellTmpl = {
        text: '',
        selected: false,
        disabled: false
      }

      for (let i = 0; i < 10; i++) {
        // const cell = deepCopy(cellTmpl)
        const cell = cloneDeep(cellTmpl)
        cell.text = this.startYear + i

        const date = new Date(this.date)
        date.setFullYear(cell.text)
        cell.disabled = typeof this.disabledDate === 'function' && this.disabledDate(date) && this.selectionMode === 'year'

        cell.selected = Number(this.year) === cell.text
        cells.push(cell)
      }

      return cells
    }
  },
  methods: {
    getCellCls (cell) {
      return [
        `${baseClass}__cell`,
        {
          [`${baseClass}__cell--selected`]: cell.selected,
          [`${baseClass}__cell--disabled`]: cell.disabled
        }
      ]
    },
    nextTenYear () {
      this.$emit('on-pick', Number(this.year) + 10, false)
    },
    prevTenYear () {
      this.$emit('on-pick', Number(this.year) - 10, false)
    },
    handleClick (event) {
      const target = event.target
      if (target.tagName === 'EM') {
        const cell = this.cells[parseInt(event.target.getAttribute('index'))]
        if (cell.disabled) return

        this.$emit('on-pick', cell.text)
      }
      this.$emit('on-pick-click')
    }
  }
}
</script>



