<template>
  <div class="x-progress-root" :class="{'border-radius': borderRadius}">
    <div class="progress" :style="progressStyle" :class="{'border-radius': borderRadius}">
      <div class="animation-mask" v-show="status === 'uploading'"></div>
    </div>
  </div>
</template>

<script>
const COLORS = {
  uploading: '#20a0ff',
  success: '#13ce66',
  fail: '#ff4949'
}

export default {
  name: 'Progress',

  props: {
    // 百分比
    percent: {
      type: Number,
      required: true
    },

    // 当前进度条状态
    status: {
      type: String,
      default: 'uploading' // success/fail/uploading
    },

    // 是否显示进度
    showText: {
      type: Boolean,
      default: true
    },

    borderRadius: {
      type: Boolean,
      default: true
    }
  },

  computed: {
    // 进度条颜色
    color () {
      return COLORS[this.status]
    },

    // 进度条样式
    progressStyle () {
      let result = {
        width: `${this.percent}%`,
        backgroundColor: this.color
      }
      return result
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-progress-root
  display inline-block
  width 100%
  height 6px
  .progress
    position relative
    height 100%
    overflow hidden
    transition all ease 0.2s
  .animation-mask
    position absolute
    top 0
    left 0
    width 100%
    height 100%
    background linear-gradient(left, rgba(255,255,255,0), rgba(255,255,255,0.3), rgba(255,255,255,0.3), rgba(255,255,255,0))
    animation play 3s infinite

  &.border-radius
    border-radius 100px
    .border-radius
      border-radius 100px


  @keyframes play
    from
      transform translateX(-100%)
    to
      transform translateX(100%)

for theme in themes
  .theme-{theme}
    .x-progress-root
      background lookup(theme + '-progress-bg-color')

</style>



