<template>
  <div :class="wrapClasses" :style="wrapStyles">
    <template v-if="type!=='textarea'">
      <div
        :class="[baseClass + '-group-prepend']"
        v-if="prepend"
        v-show="slotReady"
        ref="prepend">
        <slot name="prepend"></slot>
      </div>
      <transition name="fade">
        <icon :type="icon" :class="baseClass + '-icon'" v-if="icon" @click.native="onIconClick"></icon>
      </transition>
      <input
        :type="type"
        :class="inputClasses"
        :placeholder="placeholder"
        :disabled="disabled"
        :maxlength="maxlength"
        :readonly="readonly"
        :name="name"
        :number="number"
        :value="currentValue"
        @keyup.enter="onEnter"
        @focus="onFocus"
        @blur="onBlur"
        @input="onInput"
        @change="onChange">
      <div
        :class="[baseClass + '-group-append']"
        v-if="append"
        v-show="slotReady"
        ref="append">
        <slot name="append"></slot>
      </div>
    </template>
    <textarea
      v-else
      ref="textarea"
      :class="textareaClasses"
      :style="textareaStyles"
      :placeholder="placeholder"
      :disabled="disabled"
      :rows="rows"
      :maxlength="maxlength"
      :readonly="readonly"
      :name="name"
      :value="currentValue"
      @keyup.enter="onEnter"
      @focus="onFocus"
      @blur="onBlur"
      @input="onInput">
    </textarea>
  </div>
</template>

<script>
import Icon from '../icon/icon'
import oneOf from '../../utils/collection/one-of'
import calcTextareaHeight from '../../utils/dom/calc-textarea-height'
import Emitter from '../../mixins/emitter'

const baseClass = `x-input`

function findComponentUpward (instance, list) {
  let parent = instance.$parent || instance.$root
  let name = parent.$options.name

  while (parent && name) {
    if (list.indexOf(name) !== -1) {
      return true
    }

    parent = parent.$parent

    if (parent) {
      name = parent.$options.name
    }
  }

  return false
}

export default {
  name: 'XInput',

  mixins: [ Emitter ],

  props: {
    // 类型
    type: {
      validator (value) {
        return oneOf(value, ['text', 'textarea', 'password'])
      },
      default: 'text'
    },
    // 值
    value: {
      type: [String, Number],
      default: ''
    },
    // 宽度
    width: {
      type: Number
    },
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['small', 'large'])
      }
    },
    // 占位符
    placeholder: {
      type: String,
      default: ''
    },
    // 最大输入长度
    maxlength: {
      type: Number
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 图标
    icon: String,
    // 是否自动调整大小
    autosize: {
      type: [Boolean, Object],
      default: false
    },
    // 多行文本框行数
    rows: {
      type: Number,
      default: 2
    },
    // 是否只读
    readonly: {
      type: Boolean,
      default: false
    },
    // 名称
    name: {
      type: String
    },
    // 是否以数字对待
    number: {
      type: Boolean,
      default: false
    }
  },

  components: {
    Icon
  },

  data () {
    return {
      baseClass,
      currentValue: this.value,
      prepend: true, // 前置内容
      append: true, // 后置内容
      slotReady: false,
      textareaStyles: {}
    }
  },

  computed: {
    // 容器样式
    wrapClasses () {
      return [
        `${baseClass}-wrapper`,
        {
          [`${baseClass}-wrapper--${this.size}`]: !!this.size,
          [`${baseClass}-${this.type}`]: this.type,
          [`${baseClass}-group`]: this.prepend || this.append,
          [`${baseClass}-group--${this.size}`]: (this.prepend || this.append) && !!this.size
        }
      ]
    },
    // 单行文本框样式
    inputClasses () {
      return [
        `${baseClass}`,
        {
          [`${baseClass}--${this.size}`]: !!this.size,
          [`${baseClass}--disabled`]: this.disabled
        }
      ]
    },
    // 多行文本框样式
    textareaClasses () {
      return [
        `${baseClass}`,
        {
          [`${baseClass}--disabled`]: this.disabled
        }
      ]
    },
    wrapStyles () {
      let result = {}
      if (this.width) {
        result.width = `${this.width}px`
      }
      return result
    }
  },

  mounted () {
    this.$nextTick(() => {
      this.initialize()
    })
  },

  watch: {
    value (val) {
      this.setCurrentValue(val)
    }
  },

  methods: {
    /**
     * 初始化
     */
    initialize () {
      if (this.type !== 'textarea') {
        this.prepend = this.$slots.prepend !== undefined
        this.append = this.$slots.append !== undefined
      } else {
        this.prepend = false
        this.append = false
      }

      this.slotReady = true
      this.resizeTextarea()
    },

    /**
     * 设置当前值
     * @param {String} value [description]
     */
    setCurrentValue (value) {
      if (value === this.currentValue) return
      this.$nextTick(() => {
        this.resizeTextarea()
      })
      this.currentValue = value
      // TODO 如果输入框不嵌套于'DatePicker', 'TimePicker', 'Cascader', 'Search'组件，分发失焦事件
      // if (!findComponentUpward(this, ['DatePicker', 'TimePicker', 'Cascader', 'Search'])) {
      //   this.dispatch('XFormItem', 'fieldBlur', this.currentValue)
      // }
    },

    /**
     * 调整多行文本框的大小
     */
    resizeTextarea () {
      const autosize = this.autosize
      if (!autosize || this.type !== 'textarea') {
        return false
      }

      const minRows = autosize.minRows
      const maxRows = autosize.maxRows

      this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows)
    },

    /**
     * 处理回车确认
     * @param  {HTML DOM Event} event 事件
     */
    onEnter (event) {
      this.$emit('enter', event)
    },

    /**
     * 处理聚焦
     * @param  {HTML DOM Event} event 事件
     */
    onFocus (event) {
      this.$emit('focus', event)
    },

    /**
     * 处理失焦
     * @param  {HTML DOM Event} event 事件
     */
    onBlur (event) {
      this.$emit('blur', event)
      // TODO 如果输入框不嵌套于'DatePicker', 'TimePicker', 'Cascader', 'Search'组件，分发失焦事件
      if (!findComponentUpward(this, ['DatePicker', 'TimePicker', 'Cascader', 'Search'])) {
        this.dispatch('XFormItem', 'fieldBlur', this.currentValue)
      }
    },

    /**
     * 处理输入
     * @param  {HTML DOM Event} event 事件
     */
    onInput (event) {
      let value = event.target.value
      if (this.number) value = Number.isNaN(Number(value)) ? value : Number(value)
      this.$emit('input', value)
      this.setCurrentValue(value)
      this.$emit('value-change', event)
      this.dispatch('XFormItem', 'fieldChange')
    },

    /**
     * 处理值改变
     * @param  {HTML DOM Event} event 事件
     */
    onChange (event) {
      this.$emit('input-change', event)
    },

    /**
     * 处理图标点击
     * @param  {HTML DOM Event} event 事件
     */
    onIconClick (event) {
      this.$emit('click-icon', event)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/input'

.x-input
  input()
  &-wrapper
    display inline-block
    width 100%
    position relative
    vertical-align middle

  &-icon
    size 32px input-height-base
    line-height input-height-base
    text-align center
    font-size 16px
    color extra-light-gray
    absolute right
    z-index 1

    &:hover
      color primary-color

  &-icon + &
    padding-right 32px

  &-wrapper--large &-icon
    font-size 18px
    height input-height-large
    line-height input-height-large

  &-wrapper--small &-icon
    size 24px input-height-small
    font-size 14px
    line-height input-height-small

    + .x-input
      padding-right 24px

for theme in themes
  .theme-{theme}
    .x-input
      placeholder(lookup(theme + '-input-placeholder-color'))
      input-color(theme)

    .x-input-group
      input-group-color(theme)

.x-input-group
  input-group()
</style>



