<template>
  <div class="x-search-box">
    <XSelect
      class="x-search-box__select"
      size="small"
      v-model="currentType"
      v-if="typeOptions !== undefined && typeOptions.length">
      <XOption
        v-for="opt in typeOptions"
        :label="opt.label"
        :value="opt.value"
        :key="opt.value">
      </XOption>
    </XSelect>
    <XInput
      size="small"
      class="x-search-box__input"
      v-model="currentKeyword"
      :icon="clearIcon"
      @enter="search"
      @focus="focus = true"
      @blur="focus = false"
      @click-icon="clear">
    </XInput>
    <XButton
      size="small"
      class="x-search-box__button"
      @click.native="search">
      <Icon type="search"></Icon>
    </XButton>
  </div>
</template>

<script>
export default {
  name: 'SearchBox',

  props: {
    // 搜索类型
    type: {
      type: [String, Number],
      default: ''
    },
    // 可选的搜索类型列表
    typeOptions: {
      type: Array
    },
    // 关键词
    keyword: {
      type: String,
      required: true,
      default: ''
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    /**
     * 是否可清除
     */
    clearIcon () {
      return !!this.keyword && this.focus ? 'times-circle' : null
    }
  },

  data () {
    return {
      focus: false,
      currentType: this.type,
      currentKeyword: this.keyword
    }
  },

  watch: {
    currentType (val) {
      this.updateParam('type', val)
    },

    currentKeyword (val) {
      this.updateParam('keyword', val)
    }
  },

  methods: {
    /**
     * 搜索
     */
    search () {
      this.$emit('search', this.currentKeyword, this.currentType)
    },

    /**
     * 清空搜索框
     */
    clear () {
      this.currentKeyword = ''
      this.$emit('clear')
    },

    /**
     * 更新父组件参数
     */
    updateParam (param, val) {
      this.$emit(`update:${param}`, val)
    }
  }
}
</script>

<style lang="stylus">
@import '~@/styles/common'

.x-search-box
  display table
  width 100%
  position relative
  border-collapse seperate

  .x-search-box__select
  .x-search-box__input
  .x-search-box__button
    display table-cell

  .x-search-box__select
    min-width 90px
    width 90px
    margin-right -1px
    &.x-select--single
      .x-select__trigger
        border-bottom-right-radius 0
        border-top-right-radius 0

    & + .x-search-box__input
      .x-input
        border-radius 0
        border-left none

  .x-search-box__input
    min-width 140px
    .x-input
      border-right none

  .x-search-box__button
    min-width 32px
    width 32px
    padding-left 0
    padding-right 0
    border-bottom-left-radius 0
    border-top-left-radius 0
</style>




