<template>
  <transition :name="transitionName">
    <div :class="classes" :style="styles">
      <div :class="contentClasses" ref="content" v-html="content"></div>
      <span :class="closeIconClasses" @click="close" v-if="closable">
        <icon type="times"></icon>
      </span>
    </div>
  </transition>
</template>

<script>
import Icon from '../../icon/icon'

export default {
  name: 'Notice',

  props: {
    // 类前缀
    baseClass: {
      type: String,
      default: ''
    },
    // 生存时长
    duration: {
      type: Number,
      default: 2
    },
    // 内容
    content: {
      type: String,
      default: ''
    },
    // 样式
    styles: {
      type: Object,
      default () {
        return {
          right: '50%'
        }
      }
    },
    // 是否允许关闭
    closable: {
      type: Boolean,
      default: false
    },
    // 标识
    name: {
      type: String,
      required: true
    },
    // 回调
    onClose: {
      type: Function
    },
    // 类名
    className: {
      type: String
    },
    // 动画形式
    transitionName: {
      type: String
    }
  },

  components: {
    Icon
  },

  data () {
    return {
      withDesc: false // 默认只带标题
    }
  },

  computed: {
    // 基础样式
    prefixClass () {
      return `${this.baseClass}-notice`
    },
    // 容器样式
    classes () {
      return [
        this.prefixClass,
        {
          [`${this.className}`]: !!this.className,
          [`${this.prefixClass}--closable`]: this.closable,
          [`${this.prefixClass}--with-desc`]: this.withDesc
        }
      ]
    },
    // 内容样式
    contentClasses () {
      return `${this.prefixClass}__content`
    },
    // 关闭图标样式
    closeIconClasses () {
      return `${this.prefixClass}__close`
    }
  },

  mounted () {
    this.clearTimer()

    // 延时关闭
    if (this.duration !== 0) {
      this.timer = setTimeout(() => {
        this.close()
      }, this.duration * 1000)
    }

    // 带描述
    if (this.baseClass === 'x-notice') {
      this.withDesc = this.$refs['content'].querySelectorAll(`.${this.baseClass}__desc`)[0].innerHTML !== ''
    }
  },

  beforeDestroy () {
    this.clearTimer()
  },

  methods: {
    /**
     * 关闭提示
     */
    close () {
      this.clearTimer()
      this.onClose() // 执行回调
      this.$parent.close(this.name) // 销毁实例
    },

    /**
     * 清除计时器
     */
    clearTimer () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
    }
  }
}
</script>



