<template>
  <colgroup>
    <col v-if="checkboxWidth > 0" :width="checkboxWidth">
    <col
      v-for="(colConfig, colIndex) in colConfigs"
      :width="colConfig.computedWidth"
      >
  </colgroup>
</template>

<script>
export default {
  name: 'colgroup',

  props: {
    colConfigs: {
      type: Array
    },
    checkboxWidth: 0
  }
}
</script>



