<template>
  <div :class="classes" :style="styles">
    <notice
      v-for="notice in notices"
      :base-class="baseClass"
      :styles="notice.styles"
      :content="notice.content"
      :duration="notice.duration"
      :closable="notice.closable"
      :name="notice.name"
      :key="notice.name"
      :transition-name="notice.transitionName"
      :on-close="notice.onClose">
    </notice>
  </div>
</template>

<script>
import Notice from './notice'
import assign from 'lodash/assign'

const baseClass = 'x-notification'
let seed = 0
const now = Date.now()

function getUuid () {
  return 'xNotification_' + now + '_' + (seed++)
}

export default {
  name: 'Notification',

  components: {
    Notice
  },

  props: {
    // 类前缀
    baseClass: {
      type: String,
      default: baseClass
    },
    // 样式
    styles: {
      type: Object,
      default () {
        return {
          top: '30px',
          left: '50%'
        }
      }
    },
    // 内容
    content: {
      type: String
    },
    // 类名
    className: {
      type: String
    }
  },

  data () {
    return {
      notices: []
    }
  },

  computed: {
    // 容器类
    classes () {
      return [
        this.baseClass,
        {
          [`${this.className}`]: !!this.className
        }
      ]
    }
  },

  methods: {
    /**
     * 添加消息
     */
    add (notice) {
      const name = notice.name || getUuid()
      const defaultNotice = {
        styles: {
          right: '50%'
        },
        content: '',
        duration: 1.5,
        closable: false,
        name: name
      }

      this.notices.push(assign({}, defaultNotice, notice))
    },

    /**
     * 关闭指定消息
     * @param  {String} name 消息名称
     */
    close (name) {
      const notices = this.notices

      for (let i = 0, len = notices.length; i < len; i++) {
        if (notices[i].name === name) {
          this.notices.splice(i, 1)
          break
        }
      }
    },

    /**
     * 关闭全部消息
     */
    closeAll () {
      this.notices = []
    }
  }
}
</script>

<style lang="stylus">
@import '../../../styles/common'

// Message 消息
.x-message
  font-size font-size-base
  fixed left top 16px
  width 100%
  z-index zindex-message

  &-notice
    absolute left 50%
    vertical-align middle
    width auto

  &-notice__content
    relative right 50%
    padding 8px 20px
    border-radius border-radius-small
    display block
    box-sizing border-box

    .{icon-prefix}
      margin-right 5px
      font-size 20px
      relative top 1px

// Notice 通知
.x-notice
  position fixed
  z-index zindex-notice
  margin-right 15px
  width notice-width

  &-notice
    position relative
    padding notice-padding
    margin-bottom notice-margin-bottom
    border-radius btn-border-radius-small
    line-height 1
    overflow hidden
    box-sizing border-box

    &__close
      absolute top 15px right 15px

    &--with-desc
      .x-notice-notice__close
        top 11px

  &__title
    font-size font-size-base + 1px
    padding-right 10px
    overflow hidden
    text-overflow ellipsis
    white-space nowrap

  &--with-desc &__title
    margin-bottom 8px

  &--with-desc&--with-icon &__title
    margin-left 50px

  &__desc
    font-size font-size-small
    text-align justify
    line-height 1.5

  &--with-desc&--with-icon &__desc
    margin-left 50px

  &--with-icon &__title
    margin-left 30px

  &__icon
    absolute left 20px
    margin-top -4px
    font-size 20px

  &--with-desc &__icon
    font-size 36px

  &--custom-content
    &:after
      absolute left top bottom
      content ''
      width 4px
      display block

for theme in themes
  .theme-{theme}
    // Message 消息
    .x-message
      &-notice__content
        box-shadow lookup(theme + '-message-shadow')
        background lookup(theme + '-message-bg')
      &--success .{icon-prefix}
        color lookup(theme + '-message-success-icon-color')
      &--error .{icon-prefix}
        color lookup(theme + '-message-error-icon-color')
      &--warning .{icon-prefix}
        color lookup(theme + '-message-warning-icon-color')
      &--info .{icon-prefix}
        color lookup(theme + '-message-info-icon-color')

    // Notice 通知
    .x-notice
      &-notice
        box-shadow lookup(theme + '-notice-shadow')
        background lookup(theme + '-notice-bg')
        &__close
          color lookup(theme + '-notice-close-color')
          i
            close-base(theme, -1px)
      &__title
        color lookup(theme + '-notice-title-color')
      &__desc
        color lookup(theme + '-notice-desc-color')
      &--with-normal
        .{icon-prefix}
          color lookup(theme + '-notice-info-color')
        &:after
          background lookup(theme + '-notice-info-color')
      &--with-info
        .{icon-prefix}
          color lookup(theme + '-notice-info-color')
        &:after
          background lookup(theme + '-notice-info-color')
      &--with-success
        .{icon-prefix}
          color lookup(theme + '-notice-success-color')
        &:after
          background lookup(theme + '-notice-success-color')
      &--with-warning
        .{icon-prefix}
          color lookup(theme + '-notice-warning-color')
        &:after
          background lookup(theme + '-notice-warning-color')
      &--with-error
        .{icon-prefix}
          color lookup(theme + '-notice-error-color')
        &:after
          background lookup(theme + '-notice-error-color')
</style>



