<template>
  <button :type="htmlType" :class="classes" :disabled="disabled">
    <span v-if="showSlot" ref="slot"><slot></slot></span>
  </button>
</template>

<script>
import oneOf from '../../utils/collection/one-of'
import hasHTML from '../../utils/dom/has-html'

export default {
  name: 'XButton',

  props: {
    // 按钮类型
    type: {
      validator (value) {
        return oneOf(value, ['primary', 'ghost', 'dashed', 'text', 'info', 'success', 'warning', 'error'])
      }
    },
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['mini', 'small', 'large', 'xlarge'])
      }
    },
    // 是否加载中
    loading: Boolean,
    // 是否禁用
    disabled: Boolean,
    htmlType: {
      validator (value) {
        return oneOf(value, ['button', 'submit', 'reset'])
      },
      default: 'button'
    },
    // 是否长按钮
    long: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      showSlot: true // 是否显示按钮内容
    }
  },

  computed: {
    // 类
    classes () {
      let baseClass = 'x-btn'
      return [
        `${baseClass}`,
        {
          [`${baseClass}--${this.type}`]: !!this.type,
          [`${baseClass}--long`]: this.long,
          [`${baseClass}--${this.size}`]: !!this.size,
          [`${baseClass}--loading`]: this.loading,
          [`${baseClass}--icon-only`]: !this.showSlot && this.loading
        }
      ]
    }
  },

  mounted () {
    // 检测按钮是否存在html内容
    // console.log(this.$refs['slot'])
    this.showSlot = hasHTML(this.$refs['slot'])
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/button'

for theme in themes
  .theme-{theme}
    .x-btn {
      btn(theme)
      btn-default(theme)
      &--primary {
        btn-primary(theme)
      }

      &--ghost {
        btn-ghost(theme)
      }

      &--dashed {
        btn-dashed(theme)
      }

      &--text {
        btn-text(theme)
      }

      &--success {
        btn-full-fill(theme, lookup(theme + '-btn-success-color'))
      }

      &--warning {
        btn-full-fill(theme, lookup(theme + '-btn-warning-color'))
      }

      &--error {
        btn-full-fill(theme, lookup(theme + '-btn-error-color'))
      }
    }

.x-btn {
  &--long {
    width: 100%;
  }

  & > .{icon-prefix} + span,
  & > span + .{icon-prefix} {
    margin-left: 4px;
  }
}
</style>



