<template>
  <form class="x-form" :class="[
    labelPosition ? 'x-form--label-' + labelPosition : '',
    { 'x-form--inline': inline }
  ]">
    <slot></slot>
  </form>
</template>
<script>
  export default {
    name: 'XForm',

    componentName: 'XForm',

    props: {
      model: Object,
      rules: Object,
      labelPosition: String,
      labelWidth: String,
      labelSuffix: {
        type: String,
        default: ''
      },
      inline: Boolean,
      showMessage: {
        type: Boolean,
        default: true
      }
    },
    watch: {
      rules () {
        this.validate()
      }
    },
    data () {
      return {
        fields: []
      }
    },
    created () {
      this.$on('addField', (field) => {
        if (field) {
          this.fields.push(field)
        }
      })
      /* istanbul ignore next */
      this.$on('removeField', (field) => {
        if (field.prop) {
          this.fields.splice(this.fields.indexOf(field), 1)
        }
      })
    },
    methods: {
      resetFields () {
        this.fields.forEach(field => {
          field.resetField()
        })
      },
      validate (callback) {
        let valid = true
        let count = 0
        // 如果需要验证的fields为空，调用验证时立刻返回callback
        if (this.fields.length === 0 && callback) {
          callback(true)
        }
        this.fields.forEach((field, index) => {
          field.validate('', errors => {
            if (errors) {
              valid = false
            }
            if (typeof callback === 'function' && ++count === this.fields.length) {
              callback(valid)
            }
          })
        })
      },
      validateField (prop, cb) {
        var field = this.fields.filter(field => field.prop === prop)[0]
        if (!field) { throw new Error('must call validateField with valid prop string!') }

        field.validate('', cb)
      }
    }
  }
</script>

<style lang="stylus">
@import '../../styles/common'


.x-form {
  &--label-left {
    & .x-form-item__label {
      text-align: left
    }
  }
  &--label-top {
    & .x-form-item__label {
      float: none
      display: inline-block
      text-align: left
      padding: 0 0 10px 0
    }
  }
  &--inline {
    & .x-form-item {
      display: inline-block
      margin-right: 10px
      vertical-align: top
    }
    & .x-form-item__label {
      float: none
      display: inline-block
    }
    & .x-form-item__content {
      display: inline-block
      vertical-align: top
    }
    &.x-form--label-top .x-form-item__content {
      display: block
    }
  }
}
.x-form-item {
  margin-bottom: 22px
  clearfix()

  &__label {
    text-align: right
    vertical-align: middle
    float: left
    font-size: 14px
    line-height: 1
    padding: 11px 12px 11px 0
    box-sizing: border-box
  }
  &__content {
    line-height: 36px
    position: relative
    font-size: 14px
    clearfix()
  }
  &__error {
    font-size: 12px
    line-height: 1
    padding-top: 4px
    position: absolute
    top: 100%
    left: 0
  }

  &.is-required {
    .x-form-item__label:before {
      content: '*'
      margin-right: 4px
    }
  }
}

for theme in themes
  .theme-{theme}
    .x-form-item
      &__label
        color: lookup(theme + '-form-label-color')
      
      &__error
        color: lookup(theme + '-form-error-color')

      &.is-required {
        .x-form-item__label:before {
          color: lookup(theme + '-form-error-color')
        }
      }

      &.is-error {
        & .x-input,
        .x-select__trigger,
        .x-cascader__trigger {
          border-color: lookup(theme + '-form-error-color')

          &:hover,
          &:focus {
            border-color: lookup(theme + '-form-error-color')
          }
        }
      }
</style>



