<template>
  <modal
    width="420px"
    max-height="700px"
    class="x-modal--confirm"
    :title="title"
    :show="showModal"
    :show-close-btn="true"
    :outside-closable="false"
    @close="cancel">
    <alert show-icon naked size="large" :type="iconType">
      <template slot="desc">{{ content }}</template>
    </alert>
    <div slot="footer">
      <x-button type="text" size="small" @click.native="cancel">取消</x-button>
      <x-button type="primary" size="small" @click.native="confirm">确定</x-button>
    </div>
  </modal>
</template>

<script>
import Modal from '../modal/modal'
import Alert from '../alert/alert'
import XButton from '../button/button'

export default {
  name: 'Confirm',

  components: {
    Modal,
    Alert,
    XButton
  },

  data () {
    return {
      title: '',
      content: '',
      showModal: false,
      state: null,
      iconType: 'warning'
    }
  },

  methods: {
    /**
     * 显示弹窗
     * @param {Object|String} params 参数
     */
    show (params = {}) {
      return new Promise((resolve, reject) => {
        params = this.checkParams(params)
        let { title, content } = params
        if (!content) {
          return reject(console.error('无内容'))
        }
        this.title = title || ''
        this.content = content || ''
        this.iconType = params.type || 'warning'
        this.showModal = true
        this.state = null
        this.$watch('state', (val) => {
          if (val === null) return
          resolve(val)
        })
      })
    },

    /**
     * 显示弹窗
     * @param {Object|String} params 参数
     */
    info (params) {
      params = this.checkParams(params)
      params.type = 'info'
      return this.show(params)
    },

    /**
     * 显示弹窗
     * @param {Object|String} params 参数
     */
    success (params) {
      params = this.checkParams(params)
      params.type = 'success'
      return this.show(params)
    },

    /**
     * 显示弹窗
     * @param {Object|String} params 参数
     */
    error (params) {
      params = this.checkParams(params)
      params.type = 'error'
      return this.show(params)
    },

    /**
     * 显示弹窗
     * @param {Object|String} params 参数
     */
    warning (params) {
      params = this.checkParams(params)
      params.type = 'warning'
      return this.show(params)
    },

    /**
     * 显示弹窗
     * @param {Object|String} params 参数
     */
    checkParams (params) {
      if (typeof params === 'string') {
        params = { content: params }
      }
      return params
    },

    /**
     * 监听取消按钮
     */
    cancel () {
      this.showModal = false
      this.state = false
    },

    /**
     * 监听确定按钮
     */
    confirm () {
      this.showModal = false
      this.state = true
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../styles/common'

.x-modal--confirm
  .x-alert
    margin-bottom 0
</style>



