<template>
  <div class="x-upload-root" @dragover="onFileDropOver" @drop.prevent="onFileDrop">
    <slot>
      <div class="upload-button-box">
        <icon type="plus"></icon>
      </div>
    </slot>

    <transition name="fade">
      <div class="progress-box" v-show="isShowProgress">
        <x-progress :percent="percent" :status="progressStatus" :border-radius="false" />
      </div>
    </transition>

    <input type="file" class="input-file" @change="fileChange">

    <div class="delete-button" v-show="isShowDeleteButton" @click="onCancel">
      <icon type="close"></icon>
    </div>
  </div>
</template>

<script>
import Icon from '../icon/icon'
import XProgress from '../progress/progress'
export default {
  name: 'Upload',

  components: {
    XProgress,
    Icon
  },

  props: {
    url: String
  },

  data () {
    return {
      isShowProgress: false,
      percent: 0,
      files: [],
      isError: false
    }
  },

  computed: {
    isShowDeleteButton () {
      return this.isShowProgress || this.files.length > 0
    },
    progressStatus () {
      let result = 'uploading'
      if (this.percent >= 100) {
        result = 'success'
      }
      if (this.isError) {
        result = 'fail'
      }
      return result
    }
  },

  methods: {
    /**
     * 监听Dropover事件
     */
    onFileDropOver (e) {
      e.preventDefault()
    },

    /**
     * 监听外部文件放下
     */
    onFileDrop (e) {
      e.preventDefault()
      if (!e.dataTransfer) return
      this.fileChange(e)
    },
    /**
     * 监听点击选择文件
     */
    onSelectFile () {
      let inputs = this.$el.getElementsByClassName('input-file')
      let inputFile = inputs[inputs.length - 1]
      inputFile.click()
    },

    /**
     * 监听文件变化
     */
    async fileChange (event) {
      this.isShowProgress = true
      this.files = event.type === 'drop' ? event.dataTransfer.files : event.target.files
      await this.upLoadFile()
      this.isShowProgress = false
    },

    /**
     * 监听关闭按钮
     */
    onCancel () {
      this.isShowProgress ? this.cancelUpLoadFile() : this.deleteFile()
    },

    /**
     * 调用接口上传文件逻辑
     */
    upLoadFile () {
      console.log(this.files)
      this.percent = 0
      // TODO 执行上传文件操作 返回一个promise
      return new Promise((resolve, reject) => {
        this.tid = setInterval(() => {
          this.percent++
          if (this.percent >= 100) {
            window.clearInterval(this.tid)
            setTimeout(() => {
              resolve()
            }, 500)
          }
        }, 50)
      })
    },

    /**
     * 取消上传
     */
    cancelUpLoadFile () {
      // 取消上传
      window.clearInterval(this.tid)
      this.isError = true
      setTimeout(() => {
        this.isShowProgress = false
        this.files = []
      }, 500)
    },

    deleteFile () {
      this.files = []
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-upload-root
  position relative
  display inline-block

  .input-file
    position absolute
    top 0
    left 0
    display block
    width 100%
    height 100%
    opacity 0
    cursor pointer
  .progress-box
    position absolute
    bottom 0
    left 0
    width 100%
    height 6px
    line-height 0
  .upload-button-box
    width 100px
    height 100px
    line-height 100px
    box-sizing border-box
    border 1px dashed
    text-align center
    font-size 25px
    color #aaa
    transition all ease 0.3s
    &:hover
      background rgba(0,0,0,0.02)



  .delete-button
    position absolute
    top -10px
    right -10px
    width 20px
    height 20px
    line-height 20px
    text-align center
    font-size 15px
    background #fff
    border-radius 100px
    box-shadow 0 1px 2px rgba(0,0,0,0.2)
    color primary-color
    cursor pointer
    /*&>.icon
      border-radius 100px
      font-size 20px*/


</style>



