<template>
  <div class="x-table-root" :class="rootClasses">
    <!--表格上方内容-->
    <header class="header default-border-color clearfix" v-if="isShowHeader">
      <div class="table-header-left-box">
        <slot name="table-header-left"/>
      </div>
      <div class="table-header-right-box">
        <slot name="table-header-right"/>
      </div>
    </header>
    <!--表格内容-->
    <scroll-view 
      class="x-table-container default-border-color" 
      @resize="onContainerResize">
      <!-- 表格header -->
      <div class="table-header-container">
        <table class="header-table table" :style="tableStyle">
          <x-colgroup :checkbox-width="checkboxWidth" :col-configs="colConfigs" />
          <thead><tr class="default-border-color">
            <th v-if="showCheckBox" class="select-input-box default-border-color">
              <checkbox
                class="checkbox"
                :value="selectedAll"
                :indeterminate="checkboxIndeterminate"
                @change="onSelectAllEvent" />
            </th>
            <th
              class="default-border-color"
              v-for="(colConfig, colIndex) in colConfigs"
              :style="{textAlign: colConfig.textAlign}"
              @click="onHeaderClick(colConfig, colIndex, $event)"
              >
                <slot :name="'header-' + hump2line(colConfig.prop)">
                  <table-header-cell :col-config="colConfig" />
                  <table-header-icon :col-config="colConfig" />
                </slot>
            </th>
          </tr></thead>
        </table>
      </div>
      <!-- 表格body -->
      <div class="table-body-container">
        <table class="body-table table" :style="tableStyle" :class="{'zebra-line': zebraLine}">
          <x-colgroup :checkbox-width="checkboxWidth" :col-configs="colConfigs" />
          <tr
            class="default-border-color"
            v-for="(row, rowIndex) in data">
            <td v-if="showCheckBox" class="select-input-box default-border-color">
              <checkbox
                class="table-body-cell checkbox"
                :value="selectedRows.indexOf(row) > -1"
                @change="val => onSelectRowEvent(val, row)" />
            </td>
            <td
              class="default-border-color"
              v-for="(colConfig, colIndex) in colConfigs"
              :style="{textAlign: colConfig.textAlign}"
              @click="onBodyClick(colConfig, row, rowIndex, $event)"
              >
              <slot :name="'body-' + hump2line(colConfig.prop)">
                <div class="table-body-cell cell" :class="colConfig.class" v-html="row[colConfig.prop]"></div>
              </slot>
            </td>
          </tr>
          <!-- 无数据 -->
          <tr v-show="!data.length" class="no-data-tips-box default-border-color">
            <td
              class="default-border-color"
              :colspan="colConfigs.length">
              <slot name="no-data">
                <no-data />
              </slot>
            </td>
          </tr>
        </table>
      </div>
      <spin
        :show="loading"
        :with-mask="true"
        :color="loadingColor"
        :size="40">
      </spin>
    </scroll-view>
    <!--表格下方内容-->
    <footer class="footer clearfix">
      <div class="table-footer-left-box">
        <slot name="footer-left">
        </slot>
      </div>
      <div class="table-footer-right-box">
        <slot name="footer-right">
          <pagination 
            v-show="page.total > 0"
            :pageSize="page.pageSize"
            :total="page.total"
            :current="page.current">
          </pagination>
        </slot>
      </div>
    </footer>
    <!-- loading组件 -->
    
  </div>
</template>

<script>
import ScrollView from '../scroll-view/scroll-view'
import Checkbox from '../checkbox/checkbox'
import Spin from '../spin/spin'

import _ from 'lodash'
import XColgroup from './colgroup.vue'
import TableHeaderIcon from './table-header-icon.vue'
import TableHeaderCell from './header-cell.vue'
import NoData from './no-data.vue'

const COL_DEFAULT_WIDTH = 80
const CHECKBOX_CELL_WIDTH = 30

export default {
  name: 'x-table',

  components: {
    ScrollView,
    Spin,
    Checkbox,
    'x-colgroup': XColgroup,
    TableHeaderIcon,
    TableHeaderCell,
    NoData
  },

  props: {
    // 是否出现勾选列
    checkable: {
      type: Boolean,
      default: true
    },

    loadingColor: {
      type: String,
      default: ''
    },
    // 是否出现斑马线样式
    zebraLine: {
      type: Boolean,
      default: false
    },
    // 是否正在加载
    loading: {
      type: Boolean,
      default: false
    },
    // 分页信息
    page: {
      type: Object,
      default () {
        return {
          pageSize: 10,
          total: 0,
          current: 1
        }
      }
    },
    // 列配置
    cols: {
      type: Array,
      default () {
        return [
          {
            prop: 'id',
            label: 'ID',
            width: 100,
            class: 'tac',
            // tooltip: '提示', // 功能未完成
            textAlign: 'center',
            sort: 1 // -1 | 0 | 1 三个可选 只接收数字
          },
          {
            prop: 'name',
            label: '名称',
            width: 100
          },
          {
            prop: 'content',
            label: '内容'
          },
          {
            prop: 'handle',
            label: '操作',
            width: 80,
            textAlign: 'center'
          }
        ]
      }
    },
    // 表格数据
    data: {
      type: Array,
      default () {
        let result = []
        const COUNT = 10
        let template = {
          id: 'id',
          name: '名字',
          content: '示例数据',
          handle: '<a class="x-btn">操作</a>'
        }
        for (let i = 0; i < COUNT; i++) {
          let row = _.clone(template)
          for (var key in row) {
            if (key !== 'handle') {
              row[key] += i
            }
          }
          result.push(row)
        }
        return result
      }
    }
  },

  data () {
    return {
      selectedRows: [], // 当前已经选择的行
      colConfigs: []
    }
  },

  computed: {
    // 是否显示头部
    isShowHeader () {
      let {'table-header-left': left, 'table-header-right': right} = this.$slots
      return (left && left.length) || (right && right.length)
    },
    // 是否显示复选列
    showCheckBox () {
      return this.checkable && this.data.length > 0
    },
    // 复选列宽度
    checkboxWidth () {
      return this.showCheckBox ? CHECKBOX_CELL_WIDTH : 0
    },
    // 当前是否全选
    selectedAll () {
      return this.selectedRows.length === this.data.length
    },
    // 当前是否半选
    checkboxIndeterminate () {
      return this.selectedRows.length > 0 && this.selectedRows.length < this.data.length
    },
    // 跟节点类名
    rootClasses () {
      let result = {
        'checkable': this.checkable,
        'zebra-line': this.zebraLine
      }

      return result
    },
    // 表格宽度
    tableWidth () {
      let result = this.checkboxWidth
      this.colConfigs.forEach((col) => {
        col.computedWidth && (result += col.computedWidth)
      })
      return result
    },
    // 表格样式
    tableStyle () {
      let result = {
        width: `${this.tableWidth}px`
      }
      return result
    }
  },

  watch: {
    // 监听当前已选择的数据改变 如果改变了便抛出事件给父组件
    selectedRows (rows) {
      this.emit('selectedRowsChange', rows)
    }
  },

  mounted () {
    this.initColWidth()
  },

  methods: {
    onHeaderClick (colConfig, ...params) {
      var key = this.hump2line(colConfig.prop)
      key = key.replace(/_/g, '-')
      this.emit('header-' + key, colConfig, ...params)
    },

    onBodyClick (colConfig, ...params) {
      var key = this.hump2line(colConfig.prop)
      key = key.replace(/_/g, '-')
      console.log(params[2])
      this.emit('body-' + key, colConfig, ...params)
    },
    /**
     * 监听表格大小改变
     */
    onContainerResize (e) {
      clearTimeout(this.resizeTid)
      this.resizeTid = setTimeout(() => {
        this.initColWidth()
      }, 200)
    },

    /**
     * 监听是否全选
     */
    onSelectAllEvent (value) {
      this.selectedRows = value ? [].concat(this.data) : []
    },

    /**
     * 监听选择事件
     */
    onSelectRowEvent (value, row) {
      let index = this.selectedRows.indexOf(row)
      let selected = index > -1
      selected && !value ? this.selectedRows.splice(index, 1) : this.selectedRows.push(row)
    },

    /**
     * 初始化表格宽度
     */
    initColWidth () {
      let rootWidth = this.$el.offsetWidth - 2 // - 2是减掉左右边框
      this.rootWidth = rootWidth
      if (!rootWidth) return
      let colConfigs = this.cols
      let sumWidth = 0
      let noWidthCols = []
      colConfigs.forEach((col) => {
        col.width ? sumWidth += col.computedWidth = col.width : noWidthCols.push(col)
      })
      let restWidth = rootWidth - sumWidth - this.checkboxWidth
      noWidthCols.forEach((col, index) => {
        let colWidth = restWidth / noWidthCols.length
        colWidth < COL_DEFAULT_WIDTH && (colWidth = COL_DEFAULT_WIDTH)
        col.computedWidth = colWidth
      })

      this.colConfigs = _.clone(colConfigs)
    },

    // 驼峰转中划线
    hump2line (humpName) {
      return humpName.replace(/([A-Z])/g, '-$1').replace(/^-/, '').toLowerCase()
    },

    /**
     * 向父组件抛出事件
     */
    emit (eventName, ...params) {
      this.$emit(eventName, ...params)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-table-root
  width 100%
  height auto
  min-height 20px
  position relative
  overflow hidden

  .x-table-container
    position relative
    border-width 1px
    border-style solid
    box-sizing border-box
    width 100%
    max-height 2000px
    .table-header-container
    .table-body-container
      width 100%
      height 100%
      box-sizing border-box

  .table
    table-layout fixed
    th
    td
      text-align left
      position relative
      .cell
        padding 5px 10px
    tr:not(:first-child)
      border-top-width 1px
      border-top-style solid
    th:not(:first-child)
    td:not(:first-child)
      border-left-width 1px
      border-left-style solid

    .checkbox
      margin-right 0
      width 100%
      text-align center
      & > span
        margin-right 0

  .header
    width 100%
    height auto
    border 1px solid
    border-bottom 0
    box-sizing border-box
    .table-header-left-box
      float left
    .table-header-right-box
      float right

  .footer
    width 100%
    height auto
    margin-top 15px
    .table-footer-left-box
      float left
    .table-footer-right-box
      float right

for theme in themes
  .theme-{theme}
    .x-table-root
      background lookup(theme + '-table-background-color')
      .default-border-color
        border-color lookup(theme + '-table-border-color')
      table.table th
        background lookup(theme + '-table-th-background-color')
      .table.body-table tr:not(.no-data-tips-box):hover
        background lookup(theme + '-table-tr-hover-background-color')
      &.zebra-line .table tr:nth-child(2n + 1)
        background lookup(theme + '-table-zebra-line-background-color')

</style>



