<template>
  <div
    :class="classes"
    @ps-scroll-y="onScroll"
    @ps-scroll-x="onScroll"
    @ps-scroll-up="onScroll"
    @ps-scroll-down="onScroll"
    @ps-scroll-left="onScroll"
    @ps-scroll-right="onScroll"
    @ps-y-reach-start="onScroll"
    @ps-y-reach-end="onScroll"
    @ps-x-reach-start="onScroll"
    @ps-x-reach-end="onScroll">
    <div ref="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
// 组件作者: 邱国辉
// 参考文档 https://github.com/noraesae/perfect-scrollbar
// import Ps from 'perfect-scrollbar'
import Ps from 'perfect-scrollbar'
import 'perfect-scrollbar/dist/css/perfect-scrollbar.css'
import { CLASS_PREFIX } from '../../config'
import elementResizeEvent from 'element-resize-event'
import debounce from 'lodash/debounce'

export default {
  name: 'ScrollView',

  props: {
    // 是否禁止水平滚动
    suppressScrollX: {
      default: false
    },
    // 是否禁止垂直滚动
    suppressScrollY: {
      default: false
    },
    // 滚动速度
    wheelSpeed: {
      default: 2
    },
    // 鼠标事件是否冒泡
    wheelPropagation: {
      default: false
    },
    // 最小滑块长度
    minScrollbarLength: {
      default: 20
    },
    // 垂直滚动条位置
    scrollTop: {
      type: Number,
      default: 0
    },
    // 水平滚动条位置
    scrollLeft: {
      default: 0
    }
  },

  data () {
    return {
      initialized: false
    }
  },

  computed: {
    classes () {
      return [
        `${CLASS_PREFIX}-scroll-view-root`
      ]
    }
  },

  watch: {
    suppressScrollX (val) {
      this.init()
    },
    suppressScrollY (val) {
      this.init()
    },
    wheelSpeed (val) {
      this.init()
    },
    wheelPropagation (val) {
      this.init()
    },
    minScrollbarLength (val) {
      this.init()
    },
    scrollTop (val) {
      this.updateScrollTop(val)
    },
    scrollLeft (val) {
      this.updateScrollLeft(val)
    }
  },

  mounted () {
    this.tid = setTimeout(() => {
      // 监听内容尺寸变化
      elementResizeEvent(this.$refs.content, e => {
        this.updateContainer()
        this.$emit('resize', e, this)
      })
      this.init()
    }, 500)
  },

  updated () {
    this.reset()
  },

  beforeDestroy () {
    clearTimeout(this.tid)
    this.destroy()
  },

  methods: {
    reset: debounce(function () {
      this.destroy()
      this.init()
    }, 300),

    /**
     * 添加事件监听
     */
    onScroll (e) {
      this.$emit(e.type, e)
    },

    /**
     * 初始化Ps对象
     */
    init () {
      if (this.initialized) return
      Ps.initialize(this.$el, {
        suppressScrollX: this.suppressScrollX,
        suppressScrollY: this.suppressScrollY,
        wheelSpeed: this.wheelSpeed,
        wheelPropagation: this.wheelPropagation,
        minScrollbarLength: this.minScrollbarLength
      })
      this.initialized = true
    },

    /**
     * 更新纵轴滚动条
     * @param  { Number } scrollTop 纵轴滚动距离
     */
    updateScrollTop (scrollTop) {
      this.$el.scrollTop = scrollTop
      this.updateContainer()
    },

    /**
     * 更新横轴滚动条
     * @param  { Number } scrollLeft 横轴滚动距离
     */
    updateScrollLeft (scrollLeft) {
      this.$el.scrollLeft = scrollLeft
      this.updateContainer()
    },

    /**
     * 更新容器
     * TODO 若路由切换没有对 ScrollView 进行销毁操作，滚动条不正确渲染
     */
    updateContainer () {
      Ps.update(this.$el)
    },

    /**
     * 销毁Ps对象
     */
    destroy () {
      Ps.destroy(this.$el)
      this.initialized = false
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-scroll-view-root
  position relative
  overflow hidden
</style>



