<template>
  <transition name="fade">
    <div v-if="!closed" :class="wrapClasses">
      <div :class="iconClasses" v-if="showIcon">
        <slot name="icon">
          <icon :type="iconType"></icon>
        </slot>
      </div>
      <div class="x-alert-message-wrap">
        <div :class="messageClasses"><slot></slot></div>
        <div :class="descClasses" ref="desc"><slot name="desc"></slot></div>
      </div>
      <span :class="closeClasses" v-if="closable" @click="close">
        <slot name="close">
          <icon type="times"></icon>
        </slot>
      </span>
    </div>
  </transition>
</template>

<script>
import Icon from '../icon/icon'
import oneOf from '../../utils/collection/one-of'

const baseClass = 'x-alert'
const iconTypes = {
  'info': 'info-circle',
  'success': 'check-circle',
  'warning': 'exclamation-circle',
  'error': 'times-circle'
}

export default {
  name: 'Alert',

  components: {
    Icon
  },

  props: {
    // 类型
    type: {
      validator (value) {
        return oneOf(value, ['info', 'success', 'warning', 'error'])
      },
      default: 'info'
    },
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['large'])
      }
    },
    // 是否去掉边框，填色
    naked: {
      type: Boolean,
      default: false
    },
    // 是否行内
    inline: {
      type: Boolean,
      default: false
    },
    // 是否允许关闭
    closable: {
      type: Boolean,
      default: false
    },
    // 是否显示图标
    showIcon: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      baseClass,
      closed: false,
      desc: false
    }
  },

  computed: {
    // 容器样式
    wrapClasses () {
      return [
        baseClass,
        `${baseClass}--${this.type}`,
        {
          [`${baseClass}--with-icon`]: this.showIcon,
          [`${baseClass}--with-desc`]: this.desc,
          [`${baseClass}--${this.size}`]: !!this.size,
          [`${baseClass}--naked`]: this.naked,
          [`${baseClass}--inline`]: this.inline
        }
      ]
    },
    // 信息样式
    messageClasses () {
      return `${baseClass}__message`
    },
    // 描述样式
    descClasses () {
      return `${baseClass}__desc`
    },
    // 关闭图标样式
    closeClasses () {
      return `${baseClass}__close`
    },
    // 信息样式
    iconClasses () {
      return `${baseClass}__icon`
    },
    // 图标类型
    iconType () {
      return iconTypes[this.type]
    }
  },

  mounted () {
    this.desc = this.$slots.desc !== undefined
  },

  methods: {
    /**
     * 关闭警示
     */
    close (e) {
      this.closed = true
      this.$emit('close', e)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-alert
  display table
  width 100%
  box-sizing border-box
  position relative
  padding 6px 48px 6px 15px
  border-radius border-radius-small
  font-size font-size-small
  margin-bottom 10px
  clearfix()

  &-message-wrap
    display table-cell
    vertical-align middle
    width 100%

  &__icon
    font-size 20px
    float left
    line-height 1
    width 20px
    margin-right 10px

  &__desc
    font-size font-size-small

  &__close
    font-size font-size-small
    absolute top 8px right 15px
    overflow hidden
    cursor pointer

  &--with-desc
    padding 15px
    position relative
    border-radius border-radius-small
    margin-bottom 10px
    line-height 1.5

    .x-alert__icon
      width 32px

  &--with-desc&--with-icon
    padding 15px 48px 15px 15px

  &--with-desc &__message
    font-size 14px
    display block

  &--with-desc &__icon
    font-size 32px

  // 大尺寸
  &--large
    padding 15px 48px 15px 15px
  &--large &__message
    font-size 18px
  &--large &__desc
    font-size 14px
  &--large &__icon
    width 40px
    font-size 40px

  &--naked
    padding 8px 20px

  &--inline
    display inline-block
    width auto

    .x-alert-message-wrap
      width auto

    .x-alert__icon
      position static
      vertical-align middle

    .x-alert__message
      vertical-align middle

for theme in themes
  .theme-{theme}
    .x-alert
      color lookup(theme + '-alert-color')
      &__desc
        color lookup(theme + '-alert-desc-color')
      &--info
        border 1px solid lookup(theme + '-alert-info-border-color')
        background-color lookup(theme + '-alert-info-bg')
        .x-alert__icon
          color lookup(theme + '-alert-info-icon-color')
      &--success
        border 1px solid lookup(theme + '-alert-success-border-color')
        background-color lookup(theme + '-alert-success-bg')
        .x-alert__icon
          color lookup(theme + '-alert-success-icon-color')
      &--warning
        border 1px solid lookup(theme + '-alert-warning-border-color')
        background-color lookup(theme + '-alert-warning-bg')
        .x-alert__icon
          color lookup(theme + '-alert-warning-icon-color')
      &--error
        border 1px solid lookup(theme + '-alert-error-border-color')
        background-color lookup(theme + '-alert-error-bg')
        .x-alert__icon
          color lookup(theme + '-alert-error-icon-color')
      &__close
        .{icon-prefix}-times
          close-base(theme, -1px)
      &--with-desc
        color lookup(theme + '-alert-desc-color')
      &--with-desc &__message
        color lookup(theme + '-alert-desc-message-color')
      &--naked
        border-color transparent
        background-color transparent
</style>



