import './styles/base.styl'
import GlobalComponent from './components/global-component'

import Alert from './components/alert/alert'
import AnimateText from './components/animate-text/animate-text'
import Breadcrumb from './components/breadcrumb/breadcrumb'
import ButtonGroup from './components/button/button-group'
import Captcha from './components/captcha/captcha'
import Cascader from './components/cascader/cascader'
import Checkbox from './components/checkbox/checkbox'
import CheckboxGroup from './components/checkbox/checkbox-group'
import Confirm from './components/confirm/confirm'
import DatePicker from './components/date-picker'
import Draggable from './components/dnd/draggable'
import Dropdown from './components/dropdown/dropdown'
import DropdownItem from './components/dropdown/dropdown-item'
import DropdownMenu from './components/dropdown/dropdown-menu'
import Droppable from './components/dnd/droppable'
import GridItem from './components/grid-layout/grid-item'
import GridLayout from './components/grid-layout/grid-layout'
import Icon from './components/icon/icon'
import LoadingBar from './components/loading-bar/loading-bar'
import Message from './components/message/message'
import Modal from './components/modal/modal'
import Notice from './components/notice/notice'
import Pagination from './components/pagination/pagination'
import Radio from './components/radio/radio'
import RadioGroup from './components/radio/radio-group'
import ScrollView from './components/scroll-view/scroll-view'
import SearchBox from './components/search/search-box'
import XSlider from './components/slider/slider'
import Tabs from './components/tabs/tabs'
import TimePicker from './components/time-picker'
import Tooltip from './components/tooltip/tooltip'
import Tree from './components/tree/tree'
import Upload from './components/upload/upload'
import Validator from './components/validator/validator'
import XButton from './components/button/button'
import XCol from './components/col/col'
import XInput from './components/input/input'
import XOption from './components/select/option'
import XProgress from './components/progress/progress'
import XRow from './components/row/row'
import XSelect from './components/select/select'
import XSwitch from './components/switch/switch'
import XTable from './components/table/table'
import Spin from './components/spin/spin'
import XForm from './components/form/form'
import XFormItem from './components/form/form-item'

const components = {
  Alert,
  AnimateText,
  Breadcrumb,
  ButtonGroup,
  Captcha,
  Cascader,
  Checkbox,
  CheckboxGroup,
  Confirm,
  DatePicker,
  Draggable,
  Dropdown,
  DropdownItem,
  DropdownMenu,
  Droppable,
  GridItem,
  GridLayout,
  Icon,
  LoadingBar,
  Message,
  Modal,
  Notice,
  Pagination,
  Radio,
  RadioGroup,
  ScrollView,
  SearchBox,
  XSlider,
  Tabs,
  TimePicker,
  Tooltip,
  Tree,
  Upload,
  Validator,
  XButton,
  XCol,
  XInput,
  XOption,
  XProgress,
  XRow,
  XSelect,
  XSwitch,
  XTable,
  Spin,
  XForm,
  XFormItem
}

const install = function (Vue, opts = {}) {
  if (install.installed) return

  // 设置默认主题
  switchTheme(opts.theme || 'light')

  // 注册全局方法

  // 注册全局组件
  for (let name in components) {
    Vue.component(name, components[name])
  }

  // 注册实例方法
  Vue.prototype.$Message = Message
  Vue.prototype.$Notice = Notice
  Vue.prototype.$LoadingBar = new GlobalComponent(LoadingBar, Vue)
  Vue.prototype.$Confirm = new GlobalComponent(Confirm, Vue)
  Vue.prototype.$Spin = new GlobalComponent(Spin, Vue)
  Vue.prototype.$Validator = {}
  Vue.prototype.$switchTheme = switchTheme
}

export default {
  install
}

/**
 * 切换主题
 * @param  {String} theme 主题名称
 */
export function switchTheme (theme) {
  let classes = document.getElementsByTagName('body')[0].classList
  for (let i = 0, len = classes.length; i < len; i++) {
    let name = classes.item(i)
    if (name.indexOf('theme-') >= 0) {
      classes.remove(name)
      break
    }
  }
  classes.add(`theme-${theme}`)
}



