/**
 * 检查props参数
 */
export const checkPropsData = ({value, min, max, step, tooltip}) => {
  if (max <= min) return warn(`最大值${max}小于最小值${min}`)
  if (value < min) return warn(`当前值${value}小于最小值${min}`)
  if (value > max) return warn(`当前值${value}大于最大值${max}`)
  if (step > max - min) return warn(`步长不应超过最大值和最小值的差${max - min}`)
  if ((max - min) % step !== 0) return warn(`最大值${max}和最小值${min}的差${max - min}必须是步长${step}的整数倍`)
  if (tooltip.indexOf('{value}') === -1) return warn('tooltip属性必须包含占位符{value}')
  return true
}

function warn (str) {
  console.error(str)
  return false
}



