<template>
  <div class="x-picker-confirm">
    <span :class="timeClasses" v-if="showTime" @click="handleToggleTime">
      <template v-if="isTime">选择日期</template>
      <template v-else>选择时间</template>
    </span>
    <x-button size="mini" @click.native="handleClear">清空</x-button>
    <x-button size="mini" type="primary" @click.native="handleSuccess">确定</x-button>
  </div>
</template>

<script>
import XButton from '../../button/button'

export default {
  components: { XButton },

  props: {
    showTime: false,
    isTime: false,
    timeDisabled: false
  },

  data () {
    return {
    }
  },

  computed: {
    timeClasses () {
      return {
        'x-picker-confirm--time-disabled': this.timeDisabled
      }
    }
  },

  methods: {
    handleClear () {
      this.$emit('on-pick-clear')
    },
    handleSuccess () {
      this.$emit('on-pick-success')
    },
    handleToggleTime () {
      if (this.timeDisabled) return
      this.$emit('on-pick-toggle-time')
    }
  }
}
</script>



