<template>
  <label :class="wrapClasses">
    <span :class="checkboxClasses">
      <span
        :class="innerClasses"
        >
      </span>
      <input
        v-if="group"
        type="checkbox"
        :class="inputClasses"
        :disabled="disabled"
        :value="label"
        v-model="model"
        @change.stop.prevent="onChange">
      <input
        v-if="!group"
        type="checkbox"
        :class="inputClasses"
        :disabled="disabled"
        :checked="currentValue"
        @change.stop.prevent="onChange">
    </span><slot v-if="showSlot"><span ref="slot">{{ label }}</span></slot>
  </label>
</template>

<script>
import Emitter from '../../mixins/emitter'

const baseClass = `x-checkbox`

export default {
  name: 'Checkbox',

  mixins: [ Emitter ],

  props: {
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 值
    value: {
      type: Boolean,
      default: false
    },
    // 标签
    label: {
      type: [String, Number, Boolean]
    },
    // 半选
    indeterminate: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      model: [], // 模型
      currentValue: this.value,
      group: false, // 是否处于复选框组中
      showSlot: true // 是否显示标签内容
    }
  },

  computed: {
    // 容器样式
    wrapClasses () {
      return [
        `${baseClass}-wrapper`,
        {
          [`${baseClass}-group-item`]: this.group,
          [`${baseClass}-wrapper--checked`]: this.currentValue,
          [`${baseClass}-wrapper--disabled`]: this.disabled
        }
      ]
    },
    // 选择框样式
    checkboxClasses () {
      return [
        baseClass,
        {
          [`${baseClass}--checked`]: this.currentValue,
          [`${baseClass}--disabled`]: this.disabled,
          [`${baseClass}--indeterminate`]: this.indeterminate
        }
      ]
    },
    // 选择框样式
    innerClasses () {
      return `${baseClass}-inner`
    },
    // 原生选择框样式
    inputClasses () {
      return `${baseClass}-input`
    }
  },

  mounted () {
    if (this.$parent && this.$parent.$options.name === 'CheckboxGroup') {
      this.group = true
    }
    if (!this.group) {
      this.updateModel()
      this.showSlot = this.$slots.default !== undefined
    }
  },

  watch: {
    value (val) {
      this.updateModel()
    }
  },

  methods: {
    /**
     * 处理值变化
     * @param {HTML DOM Event} event 事件
     */
    onChange (event) {
      if (this.disabled) {
        return false
      }

      const checked = event.target.checked
      this.currentValue = checked
      this.$emit('input', checked)

      if (this.group) {
        this.$parent.onChange(this.model)
      } else {
        this.$emit('change', checked)
        //  在表单中使用还需要抛表单改变的事件
        this.dispatch('XFormItem', 'fieldChange')
      }
    },

    /**
     * 值更新
     */
    updateModel () {
      this.currentValue = this.value
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

// 正常状态
.x-checkbox-wrapper
  font-size font-size-small
  vertical-align middle
  display inline-block
  position relative
  white-space nowrap
  margin-right 8px
  cursor pointer

  &--disabled
    cursor not-allowed

.x-checkbox-wrapper + span,
.x-checkbox + span
  margin-right 4px

.x-checkbox
  display inline-block
  margin-right 4px
  white-space nowrap
  outline none
  position relative
  line-height 1
  vertical-align middle
  cursor pointer

  &-inner
    display inline-block
    size 14px
    relative top left
    border-radius 2px
    border 1px solid
    transition all transition-time ease-in-out
    box-sizing border-box

    &:after
      absolute left 4px top 1px
      content ' '
      size 4px 8px
      display table
      border 2px solid
      border-top 0
      border-left 0
      transform rotate(45deg) scale(0)
      transition all transition-time ease-in-out
      box-sizing border-box

  &-input
    absolute left top bottom right
    z-index 1
    opacity 0
    cursor pointer

// 选中状态
.x-checkbox--checked
  .x-checkbox-inner
    &:after
      transform rotate(45deg) scale(1)

// 禁用状态
.x-checkbox--disabled
  &.x-checkbox--checked
    .x-checkbox-inner
      &:after
        animation-name none

  .x-checkbox-inner
    cursor not-allowed
    &:after
      animation-name none

  .x-checkbox-input
    cursor not-allowed
    // cursor default

  & + span
    cursor not-allowed

// 半选状态
.x-checkbox--indeterminate
  .x-checkbox-inner:after
    content ''
    size 8px 1px
    transform scale(1)
    absolute left 2px top 5px


for theme in themes
  .theme-{theme}
    // 正常状态
    .x-checkbox
      &:hover
        .x-checkbox-inner
          border-color lookup(theme + '-checkbox-default-hover-border-color')

      &-inner
        background-color lookup(theme + '-checkbox-default-background-color')
        border-color lookup(theme + '-checkbox-default-border-color')

        &:after
          border-color lookup(theme + '-checkbox-default-hook-color')


    // 选中状态
    .x-checkbox--checked
      &:hover
        .x-checkbox-inner
          border-color lookup(theme + '-checkbox-checked-hover-border-color')

      .x-checkbox-inner
        border-color lookup(theme + '-checkbox-checked-border-color')
        background-color lookup(theme + '-checkbox-checked-background-color')

    // 禁用状态
    .x-checkbox--disabled
      &.x-checkbox--checked
        &:hover
          .x-checkbox-inner
            border-color lookup(theme + '-checkbox-disabled-hover-border-color')

        .x-checkbox-inner
          background-color lookup(theme + '-checkbox-disabled-background-color')
          border-color lookup(theme + '-checkbox-disabled-border-color')

          &:after
            border-color lookup(theme + '-checkbox-disabled-hook-color')

        // .x-checkbox-inner
        //   border-color blue

      // .x-checkbox-inner
      //   background-color #F3F3F3
      //   border-color border-color-base

      //   &:after
      //     border-color blue

      & + span
        color lookup(theme + '-checkbox-disabled-label-color')

    // 半选状态
    .x-checkbox--indeterminate
      &:hover
        .x-checkbox-inner
          border-color lookup(theme + '-checkbox-indeterminate-hover-border-color')
      .x-checkbox-inner
        background-color lookup(theme + '-checkbox-indeterminate-background-color')
        border-color lookup(theme + '-checkbox-indeterminate-border-color')
</style>



