<template>
  <div class="x-validator-root" :class="rootClasses">
    <slot></slot>
  </div>
</template>

<script>
/**
内置验证器: (rules)
  {
    required: 是否必填 (Boolean),
    max: 最大值 (Number),
    min: 最小值 (Number),
    maxlength: 最大长度 (Number),
    minlength: 最小长度 (Number),
    number: 是否必须是数字 (Boolean),
    integer: 是否必须是整数 (Boolean),
    phone: 是否是手机号码 (Boolean),
    email: 是否是邮箱 (Boolean),
    url: 是否是url (Boolean),
    regexp: 自定义正则表达式 (RegExp),
    method: 自定义验证函数 (Function)
  }
对外暴露对象
{
  formItems: [
    value: 当前表单的value值
    name: 当前表单定义的name属性
    rules: 当前表单的规则
    delay: 验证延时时间
    messages: 当前表单每个规则不通过的提示信息
    elm: 当前表单的DOM节点
    vNode: 当前表单的虚拟节点
    isComponent: 当前表单是否为组件
    dirty: 当前表单是否脏 (是否已经经过用户操作, 判断依据是光标是否进入过)
    results: 当前表单的验证结果
    errors: 当前表单验证不通过的所有信息
    isRight: 当前表单是否全部验证通过
    isError: 当前表单是否验证不通过
    isShowError: 是否显示错误提示
  ]
  isError: 布尔值 验证是否全部通过(验证不通过不一定有错误提示, 有错误提示表示验证肯定不通过)
  isShowError: 布尔值 是否存在错误提示(验证不通过不一定有错误提示, 有错误提示表示验证肯定不通过)
  reset: 静态方法 重置表单(去掉所有错误提示)
  validate: 静态方法 验证某一项或者验证全部
}
*/
// dom操作 添加和移除类名工具函数
import { addClass, removeClass } from '../../utils/dom/class'

// 默认提示文案定义
const DEFAULT_ERROR_MESSAGE = '格式不符合规范'
const ERROR_MESSAGE = {
  // 基本验证规则提示文案
  'required': '当前项为必填项, 不能留空',
  'max': '不能大于{value}',
  'min': '不能小于{value}',
  'maxlength': '长度不能超过{value}',
  'minlength': '长度不能小于{value}',
  'number': '请填入数字',
  'integer': '请填入整数',
  // 内置验证规则提示文案
  'phone': '请填入正确的手机号码',
  'email': '请输入正确的邮箱',
  'url': '请输入正确的链接',
  // 自定义拓展验证规则提示文案
  'regexp': DEFAULT_ERROR_MESSAGE,
  'method': DEFAULT_ERROR_MESSAGE

  /**
   * 如果需要添加验证规则, 只需要在这里添加验证提示文案, 然后在methods最下方按照格式添加验证函数即可
   */
}

// 类名定义
const VALIDATOR_CLASSNAME = {
  // 获得光标类名
  FOCUS: 'validator-focus',
  // 验证通过类名
  PASS: 'validator-pass',
  // 验证不通过类名
  ERROR: 'validator-error',

  // 错误信息盒子基本类名
  ERROR_MESSAGE_CONTAINER: 'validator-error-message-container'
}
export default {
  name: 'Validator',

  props: {
    // 表单名称
    name: {
      type: String,
      default: 'default',
      required: true
    },

    // 是否由组件提供默认样式
    useDefaultStyle: {
      type: Boolean,
      default: true
    },

    // 验证延时时间
    delay: {
      type: Number,
      default: 500
    }
  },

  data () {
    return {
      // 存放获取到的带有rules属性的虚拟dom
      formVNodes: [],
      // 当前所有显示错误提示的表单元素名字
      showErrorFormItemNames: new Set()
    }
  },

  computed: {
    // 需要验证的组件列表
    formItems () {
      let result = []
      this.formVNodes.forEach((vNode) => {
        if (!vNode) return
        let vNodeIsComponent = !!vNode.componentInstance
        let formItem = {}
        // 当前表单的value值
        formItem.value = vNodeIsComponent ? vNode.componentInstance.currentValue : vNode.elm.value
        // 当前表单定义的name属性
        formItem.name = vNodeIsComponent ? vNode.componentInstance.name : vNode.data.attrs.name
        // 当前表单的规则
        formItem.rules = this.getRules(vNode.data.attrs)
        // 验证延时时间
        formItem.delay = vNode.data.attrs.delay - 0 || this.delay
        // 当前表单每个规则不通过的提示信息
        formItem.messages = this.getErrorMessage(vNode.data.attrs)
        // 当前表单的DOM节点
        formItem.elm = vNode.elm
        // 当前表单的虚拟节点
        formItem.vNode = vNode
        // 当前表单是否为组件
        formItem.isComponent = vNodeIsComponent
        // 当前表单是否脏 (是否已经经过用户操作, 判断依据是光标是否进入过)
        formItem.dirty = !!vNode.dirty
        // 当前表单的验证结果
        formItem.results = this.getValidateResult(formItem)
        // 当前表单验证不通过的所有信息
        formItem.errors = this.getValidateError(formItem)
        // 当前表单是否全部验证通过
        formItem.isRight = formItem.errors.length === 0
        // 当前表单是否验证不通过
        formItem.isError = formItem.errors.length > 0

        formItem.isShowError = this.showErrorFormItemNames.has(formItem.name)

        result.push(formItem)
      })
      return result
    },

    // 根元素类名
    rootClasses () {
      let result = {
        [this.name]: true,
        'validator-default-style': this.useDefaultStyle
      }
      return result
    }
  },

  mounted () {
    this.init()
  },

  // 实例销毁之前
  beforeDestroy () {
    // 重置表单
    this.resetValidator()
    // 清空当前表单在全局挂载的信息
    this.destroyCurrentValidator()
  },

  methods: {
    /**
     * 初始化
     */
    init () {
      // 获取需要验证的虚拟dom
      this.getFormVNodes(this.$slots.default)
      // 给每个需要验证的dom添加光标进入和离开事件
      this.listenerFocusAndBlur()
      // 监听input事件
      this.listenerInput()
      // 设置全局$Validator
      this.setGlobalValidator()
    },

    /**
     * 设置全局$Validator对象的内容
     */
    setGlobalValidator () {
      // 检测是否有同名表单 有的话警告
      if (this.$Validator[this.name]) console.warn(`发现同名验证器: ${this.name}, 每个验证器的名字必须唯一!, 否则后定义的验证器将覆盖前面的同名验证器`)

      var self = this
      this.$Validator[this.name] = {
        // 每次获取formItems重新取得当前最新的值
        get formItems () { return self.formItems },
        // 所有错误信息
        // get errors () { return self.getFormErrors() },
        // 当前是否有错误
        get isError () { return self.getFromIsError() },
        // 当前是否显示错误信息
        get isShowError () { return self.showErrorFormItemNames.size > 0 }
      }
      // 添加静态方法 验证
      this.$Validator[this.name].validate = this.validate.bind(this)
      // 添加静态方法 重置
      this.$Validator[this.name].reset = this.resetValidator.bind(this)
    },

    /**
     * 递归获取包含定义了规则rules的slot元素
     */
    getFormVNodes (vNodes) {
      vNodes && vNodes.forEach(vNode => {
        vNode.data && vNode.data.attrs && vNode.data.attrs.rules && this.formVNodes.push(vNode)
        vNode.children && this.getFormVNodes(vNode.children)
      })
    },

    /**
     * 监听鼠标进入和离开事件
     */
    listenerFocusAndBlur () {
      this.formItems.forEach(vNode => {
        // 光标进入后添加focus类名
        vNode.elm.addEventListener('focus', () => {
          vNode.dirty = true
          addClass(vNode.elm, VALIDATOR_CLASSNAME.FOCUS)
        }, true)
        // 鼠标离开后移除focus类名 并且验证表单
        vNode.elm.addEventListener('blur', () => {
          vNode.dirty = true
          removeClass(vNode.elm, VALIDATOR_CLASSNAME.FOCUS)
          this.validate(vNode.name)
        }, true)
      })
    },

    /**
     * 监听表单内容改变
     */
    listenerInput () {
      this.formItems.forEach(vNode => {
        vNode.elm.addEventListener('input', () => {
          window.clearTimeout(this[`${vNode.name}TimeoutId`])
          this[`${vNode.name}TimeoutId`] = setTimeout(() => {
            this.validate(vNode.name)
          }, vNode.delay)
        }, true)
      })
    },

    /**
     * 销毁当前验证器
     */
    destroyCurrentValidator () {
      this.$Validator[this.name] = null
    },

    /**
     * 验证某一个表单元素
     */
    validate (name) {
      if (name === undefined) return this.validateAll()
      // 获取当前表单
      let currFormItem = this.getCurrentFormItemMsg(name)
      if (!currFormItem) return
      // 将当前表单玷污
      currFormItem.dirty = true

      // 添加标志是否通过验证的类名
      if (currFormItem.isRight) {
        removeClass(currFormItem.elm, VALIDATOR_CLASSNAME.ERROR)
        addClass(currFormItem.elm, VALIDATOR_CLASSNAME.PASS)
        this.removeErrorMessageElement(currFormItem)
        this.showErrorFormItemNames.delete(currFormItem.name)
      } else {
        removeClass(currFormItem.elm, VALIDATOR_CLASSNAME.PASS)
        addClass(currFormItem.elm, VALIDATOR_CLASSNAME.ERROR)
        this.createErrorMessageElement(currFormItem)
        this.showErrorFormItemNames.add(currFormItem.name)
      }
    },

    /**
     * 验证当前表单所有元素
     */
    validateAll () {
      this.formItems.forEach(item => {
        item.name && this.validate(item.name)
      })
    },

    /**
     * 创建错误提示dom
     */
    createErrorMessageElement (formItem) {
      let { message: errorMessage, name } = formItem.errors[0]
      if (!errorMessage) return
      // 移除已有错误信息盒子
      this.removeErrorMessageElement(formItem)

      // 创建dom元素
      let el = formItem.elm
      let containerClassName = `${VALIDATOR_CLASSNAME.ERROR_MESSAGE_CONTAINER} ${formItem.name}`
      let parent = el.parentElement
      let errorMessageContainer = document.createElement('div')
      let errorMessageSpan = document.createElement('span')
      errorMessageContainer.className = containerClassName
      errorMessageSpan.innerHTML = errorMessage.replace('{value}', formItem.rules[name])
      errorMessageContainer.appendChild(errorMessageSpan)

      // 设置样式
      let top = el.offsetTop + el.offsetHeight
      let left = el.offsetLeft
      let width = el.offsetWidth
      errorMessageContainer.style.left = left + 'px'
      errorMessageContainer.style.top = top + 'px'
      errorMessageContainer.style.width = width + 'px'
      parent.appendChild(errorMessageContainer)
    },

    /**
     * 移除错误信息容器
     */
    removeErrorMessageElement (formItem) {
      let parentElement = formItem.elm.parentElement
      let errorMessageContainerClassName = `${VALIDATOR_CLASSNAME.ERROR_MESSAGE_CONTAINER} ${formItem.name}`
      let errorMessageContainer = parentElement.getElementsByClassName(errorMessageContainerClassName)[0]
      errorMessageContainer && parentElement.removeChild(errorMessageContainer)
    },

    /**
     * 重置表单
     */
    resetValidator () {
      this.formItems.forEach(item => {
        item.dirty = false
        this.removeErrorMessageElement(item)
        window.clearTimeout(this[`${item.name}TimeoutId`])
      })

      // 移除当前组件的提示类名
      let validatorRootElement = document.querySelector(`.x-validator-root.${this.name}`)
      // 要移除的类名
      let classNames = [VALIDATOR_CLASSNAME.PASS, VALIDATOR_CLASSNAME.ERROR]
      classNames.forEach((className) => {
        let doms = [...validatorRootElement.getElementsByClassName(className)]
        doms.length && doms.forEach(dom => {
          removeClass(dom, className)
        })
      })

      this.showErrorFormItemNames.clear()
    },

    /**
     * 获取某个表单信息
     */
    getCurrentFormItemMsg (name) {
      let currFormItem = null
      // 强制让formItems重新计算最新值
      this.updateFormItems()
      this.formItems.forEach((formItem) => {
        if (formItem.name === name && name) {
          currFormItem = formItem
        }
      })
      if (!currFormItem) return console.warn(`表单${this.name}无法找到表单元素${name}`)

      return currFormItem
    },

    /**
     * 强制让formItems重新计算
     */
    updateFormItems () {
      /* *** 强制让计算属性formItems重新计算 Start ***/
      this.formVNodes.push(null)
      setTimeout(() => {
        // 恢复
        !this.formVNodes[this.formVNodes.length - 1] && this.formVNodes.pop()
      }, 0)
      /* *** 强制让计算属性formItems重新计算 end ***/
    },

    /**
     * 格式化规则
     */
    getRules ({rules}) {
      let result = {}
      for (var key in rules) {
        if (typeof rules[key] === 'object' && rules[key].value !== undefined) {
          result[key] = rules[key].value
        } else {
          result[key] = rules[key]
        }
      }
      return result
    },

    /**
     * 格式化错误信息
     */
    getErrorMessage ({messages, rules}) {
      if (typeof messages === 'object') return messages
      let result = {}
      for (var key in rules) {
        let message = rules[key].message
        if (message === undefined) message = ERROR_MESSAGE[key] || DEFAULT_ERROR_MESSAGE
        result[key] = message
      }
      return result
    },

    /**
     * 获取验证结果
     */
    getValidateResult ({value, rules = {}}) {
      let result = {}
      // 先验证是否为空
      let isNull = !(value + '')
      result.isNull = isNull

      // 验证其他逻辑
      for (var ruleName in rules) {
        // 判断能否找到当前验证规则
        if (this[`${ruleName}IsRight`]) {
          result[ruleName] = this[`${ruleName}IsRight`](value, rules[ruleName], isNull)
        } else {
          console.warn(`无法找到${ruleName}验证规则`)
        }
      }
      return result
    },

    /**
     * 获取当前表单的验证错误信息
     */
    getValidateError ({results, messages}) {
      let errors = []
      for (var errorKey in results) {
        if (errorKey !== 'isNull' && !results[errorKey]) {
          errors.push({
            name: errorKey,
            message: messages[errorKey] || ''
          })
        }
      }
      return errors
    },

    /**
     *  获取当前表单的搜有错误信息
     */
    getFormErrors () {
      this.updateFormItems()
      let result = {}
      this.formItems.forEach(item => {
        item.isError && (result[item.name] = item.errors)
      })
      return result
    },

    /**
     *  获取当前表单是否有错误
     */
    getFromIsError () {
      return !!Object.keys(this.getFormErrors()).length
    },

    /**
     * 验证是否为空
     */
    ['required' + 'IsRight'] (value, rule, isNull) {
      return rule ? !isNull : true
    },

    /**
     * 验证正则表达式
     */
    ['regexp' + 'IsRight'] (value, rule, isNull) {
      let reg = new window.RegExp(rule)
      return reg.test(value)
    },

    /**
     * 验证自定义方法
     */
    ['method' + 'IsRight'] (value, rule, isNull) {
      if (typeof rule !== 'function') return console.warn('method必须是一个可执行的函数')
      return rule(value)
    },

    /**
     * 验证最大值
     */
    ['max' + 'IsRight'] (value, rule, isNull) {
      return Number(value) <= Number(rule)
    },

    /**
     * 验证最小值
     */
    ['min' + 'IsRight'] (value, rule, isNull) {
      return Number(value) >= Number(rule)
    },

    /**
     * 验证最大长度
     */
    ['maxlength' + 'IsRight'] (value, rule, isNull) {
      return String(value).length <= Number(rule)
    },

    /**
     * 验证最小长度
     */
    ['minlength' + 'IsRight'] (value, rule, isNull) {
      return String(value).length >= Number(rule)
    },

    /**
     * 验证是否为数字
     */
    ['number' + 'IsRight'] (value, rule, isNull) {
      return rule ? Number(value) === value - 0 : true
    },

    /**
     * 验证是否为整数
     */
    ['integer' + 'IsRight'] (value, rule, isNull) {
      if (!rule) return true
      let isNumber = this.numberIsRight(value, rule, isNull)
      let result = isNumber && value - 0 === parseInt(value)
      return result
    },

    /**
     * 验证是否为手机
     */
    ['phone' + 'IsRight'] (value, rule, isNull) {
      if (!rule) return true
      let reg = /^1[3|4|5|8][0-9]\d{8}$/
      let result = reg.test(value)
      return result
    },

    /**
     * 验证是否为邮箱
     */
    ['email' + 'IsRight'] (value, rule, isNull) {
      if (!rule) return true
      let reg = /^([a-zA-Z0-9]+[_|_|.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|_|.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
      let result = reg.test(value)
      return result
    },

    /**
     * 验证是否为url
     */
    ['url' + 'IsRight'] (value, rule, isNull) {
      if (!rule) return true
      let reg = /^((ht|f)tps?):\/\/([\w-]+(\.[\w-]+)*\/)*[\w-]+(\.[\w-]+)*\/?(\?([\w\-.,@?^=%&:/~+#]*)+)?/
      let result = reg.test(value)
      return result
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.validator-error-message-container
  position absolute
  top 100%
  left 0
  height 25px
  line-height 25px
  font-size 12px
  overflow hidden

for theme in themes
  .theme-{theme}
    .x-validator-root
      &.validator-default-style .validator-focus
        box-shadow lookup(theme + '-validator-focus-box-shadow')
      &.validator-default-style .validator-error
        box-shadow lookup(theme + '-validator-error-box-shadow')
    .validator-error-message-container
      color lookup(theme + '-validator-error-message-color')
</style>



