<template>
  <li :class="classes" @click="onClick"><slot></slot></li>
</template>

<script>
const baseClass = `x-dropdown-item`

export default {
  props: {
    // 标识
    name: {
      type: [String, Number]
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 是否选中
    selected: {
      type: Boolean,
      default: false
    },
    // 是否带分隔线
    divided: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    classes () {
      return [
        baseClass,
        {
          [`${baseClass}--disabled`]: this.disabled,
          [`${baseClass}--selected`]: this.selected,
          [`${baseClass}--divided`]: this.divided
        }
      ]
    }
  },

  methods: {
    onClick () {
      const grandpa = this.$parent.$parent.$parent
      const hasChildren = this.$parent && this.$parent.$options.name === 'Dropdown'

      if (this.disabled) {
        this.$nextTick(() => {
          grandpa.visible = true
        })
      } else if (hasChildren) {
        this.$parent.$emit('haschildren-click')
      } else {
        if (grandpa && grandpa.$options.name === 'Dropdown') {
          grandpa.$emit('hover-click')
        }
      }

      grandpa.$emit('item-click', this.name)
    }
  }
}
</script>

<style lang="stylus">
</style>



