<template>
  <div ref="item" class="x-grid-layout" :style="layoutStyles">
    <slot></slot>
    <grid-item
      class="x-grid-placeholder"
      v-show="isDragging"
      :x="placeholder.x"
      :y="placeholder.y"
      :w="placeholder.w"
      :h="placeholder.h"
      :i="placeholder.i">
    </grid-item>
  </div>
</template>

<script>
import Vue from 'vue'
var elementResizeDetectorMaker = require('element-resize-detector')

import {bottom, compact, getLayoutItem, moveElement, validateLayout} from './utils'
// var eventBus = require('./eventBus')
import GridItem from './grid-item'

export default {
  name: 'GridLayout',
  provide: {
    eventBus: null
  },
  components: {
    GridItem
  },
  props: {
    // If true, the container height swells and contracts to fit contents
    autoSize: {
      type: Boolean,
      default: true
    },
    colNum: {
      type: Number,
      default: 12
    },
    rowHeight: {
      type: Number,
      default: 150
    },
    maxRows: {
      type: Number,
      default: Infinity
    },
    margin: {
      type: Array,
      default: function () {
        return [10, 10]
      }
    },
    isDraggable: {
      type: Boolean,
      default: true
    },
    isResizable: {
      type: Boolean,
      default: true
    },
    useCssTransforms: {
      type: Boolean,
      default: true
    },
    verticalCompact: {
      type: Boolean,
      default: true
    },
    layout: {
      type: Array,
      required: true
    }
  },
  data: function () {
    return {
      width: null,
      layoutStyles: {},
      lastLayoutLength: 0,
      isDragging: false,
      placeholder: {
        x: 0,
        y: 0,
        w: 0,
        h: 0,
        i: 0
      }
    }
  },
  created () {
    var self = this

    // Accessible refernces of functions for removing in beforeDestroy
    self.resizeEventHandler = function (eventType, i, x, y, h, w) {
      self.resizeEvent(eventType, i, x, y, h, w)
    }

    self.dragEventHandler = function (eventType, i, x, y, h, w) {
      self.dragEvent(eventType, i, x, y, h, w)
    }

    self._provided.eventBus = new Vue()
    self.eventBus = self._provided.eventBus
    self.eventBus.$on('resizeEvent', self.resizeEventHandler)
    self.eventBus.$on('dragEvent', self.dragEventHandler)
  },
  beforeDestroy () {
    // Remove listeners
    this.eventBus.$off('resizeEvent', self.resizeEventHandler)
    this.eventBus.$off('dragEvent', self.dragEventHandler)
    window.removeEventListener('resize', self.onWindowResize)
  },
  mounted () {
    this.$nextTick(() => {
      validateLayout(this.layout)
      var self = this
      this.$nextTick(function () {
        if (self.width === null) {
          self.onWindowResize()
          // self.width = self.$el.offsetWidth
          window.addEventListener('resize', self.onWindowResize)
        }
        compact(self.layout, self.verticalCompact)

        self.updateHeight()
        self.$nextTick(function () {
          var erd = elementResizeDetectorMaker({
            strategy: 'scroll' // <- For ultra performance.
          })
          erd.listenTo(self.$refs.item, function (element) {
            self.onWindowResize()
          })
        })
      })
      window.onload = function () {
        if (self.width === null) {
          self.onWindowResize()
          // self.width = self.$el.offsetWidth
          window.addEventListener('resize', self.onWindowResize)
        }
        compact(self.layout, self.verticalCompact)

        self.updateHeight()
        self.$nextTick(function () {
          var erd = elementResizeDetectorMaker({
            strategy: 'scroll' // <- For ultra performance.
          })
          erd.listenTo(self.$refs.item, function (element) {
            self.onWindowResize()
          })
        })
      }
    })
  },
  watch: {
    width () {
      this.$nextTick(function () {
        this.eventBus.$emit('updateWidth', this.width)
        this.updateHeight()
      })
    },
    layout () {
      this.layoutUpdate()
    },
    rowHeight () {
      this.eventBus.$emit('setRowHeight', this.rowHeight)
    },
    isDraggable () {
      this.eventBus.$emit('setDraggable', this.isDraggable)
    },
    isResizable () {
      this.eventBus.$emit('setResizable', this.isResizable)
    }
  },
  methods: {
    layoutUpdate () {
      if (this.layout !== undefined && this.layout.length !== this.lastLayoutLength) {
        this.lastLayoutLength = this.layout.length
        compact(this.layout, this.verticalCompact)

        // this.$broadcast('updateWidth', this.width)
        this.eventBus.$emit('updateWidth', this.width)
        this.updateHeight()
      }
    },
    updateHeight () {
      this.layoutStyles = {
        height: this.containerHeight()
      }
    },
    onWindowResize () {
      if (this.$refs !== null && this.$refs.item !== null && this.$refs.item !== undefined) {
        this.width = this.$refs.item.offsetWidth
      }
    },
    containerHeight () {
      if (!this.autoSize) return
      return bottom(this.layout) * (this.rowHeight + this.margin[1]) + this.margin[1] + 'px'
    },
    dragEvent (eventName, id, x, y, h, w) {
      if (eventName === 'dragmove' || eventName === 'dragstart') {
        this.isDragging = true
        this.placeholder.i = id
        this.placeholder.x = x
        this.placeholder.y = y
        this.placeholder.w = w
        this.placeholder.h = h
        // this.$broadcast('updateWidth', this.width)
        this.eventBus.$emit('updateWidth', this.width)
      } else {
        this.isDragging = false
      }
      // console.log(eventName + ' id=' + id + ', x=' + x + ', y=' + y)
      var l = getLayoutItem(this.layout, id)
      // GetLayoutItem sometimes returns null object
      if (l === null) {
        l = { x: 0, y: 0 }
      }
      l.x = x
      l.y = y
      // Move the element to the dragged location.
      this.layout = moveElement(this.layout, l, x, y, true)
      compact(this.layout, this.verticalCompact)
      // needed because vue can't detect changes on array element properties
      this.eventBus.$emit('compact')
      this.updateHeight()
    },
    resizeEvent (eventName, id, x, y, h, w) {
      if (eventName === 'resizestart' || eventName === 'resizemove') {
        this.isDragging = true
        this.placeholder.i = id
        this.placeholder.x = x
        this.placeholder.y = y
        this.placeholder.w = w
        this.placeholder.h = h
        // this.$broadcast('updateWidth', this.width)
        this.eventBus.$emit('updateWidth', this.width)
      } else {
        this.isDragging = false
      }
      var l = getLayoutItem(this.layout, id)
      // GetLayoutItem sometimes return null object
      if (l == null) {
        l = { h: 0, w: 0 }
      }
      l.h = h
      l.w = w
      compact(this.layout, this.verticalCompact)
      this.eventBus.$emit('compact')
      this.updateHeight()
    }
  }
}
</script>

<style lang="stylus">
.x-grid-layout
  position relative
  transition height .2s ease
</style>



