<template>
  <div :class="classes" :style="wrapStyles" @click="toggle">
    <slot name="on" v-if="currentValue"></slot>
    <slot name="off" v-if="!currentValue"></slot>
    <div class='x-switch__button' :style="buttonStyles"></div>
  </div>
</template>

<script>
import oneOf from '../../utils/collection/one-of'

export default {
  name: 'Switch',

  props: {
    // 值
    value: {
      type: Boolean,
      default: false
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 宽度
    width: {
      type: Number
    },
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['small'])
      }
    },
    // 类型
    type: {
      validator (value) {
        return oneOf(value, ['primary', 'success'])
      },
      default: 'primary'
    }
  },

  data () {
    return {
      currentValue: this.value
    }
  },

  computed: {
    classes () {
      return [
        'x-switch',
        {
          [`x-switch--checked`]: this.currentValue,
          [`x-switch--disabled`]: this.disabled,
          [`x-switch--${this.size}`]: !!this.size,
          [`x-switch--${this.type}`]: !!this.type
        }
      ]
    },
    wrapStyles () {
      let styles = {}
      if (this.width && this.width > 0) {
        styles.width = `${this.width}px`
      }
      return {
        width: `${this.width || this.defaultWidth}px`
      }
    },
    defaultWidth () {
      return ({
        normal: 48,
        small: 32
      })[this.size || 'normal']
    },
    defaultButtonSize () {
      return ({
        normal: 20,
        small: 12
      })[this.size || 'normal']
    },
    buttonStyles () {
      let styles = {}
      let w = this.width || this.defaultWidth
      if (this.currentValue) {
        styles.left = `${w - this.defaultButtonSize - 2}px`
      }
      return styles
    }
  },

  watch: {
    value (val) {
      this.currentValue = val
    }
  },

  methods: {
    /**
     * 切换开关状态
     */
    toggle () {
      if (this.disabled) {
        return false
      }
      const checked = !this.currentValue
      this.currentValue = checked
      this.$emit('input', checked)
      this.$emit('change', checked)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-switch
  display inline-block
  vertical-align middle
  height 24px
  border-radius 24px
  line-height 22px
  box-sizing border-box
  cursor pointer
  user-select none
  position relative
  font-size font-size-small
  padding-left 20px
  padding-right 5px
  text-align center
  transition all transition-time ease-in-out

  i
    size 12px
    text-align center

  &__button
    content ''
    size 20px
    border-radius 20px
    absolute left 2px top 2px
    cursor pointer
    transition left transition-time ease-in-out

  &--small
    height 16px
    line-height 10px
    .x-switch__button
      size 12px

  &--checked
    padding-right 20px
    padding-left 5px

  &--disabled
    cursor not-allowed

    .x-switch__button
      cursor not-allowed


for theme in themes
  .theme-{theme}
    // 正常状态
    .x-switch
      background-color lookup(theme + '-switch-background-color')
      color lookup(theme + '-switch-color')

    .x-switch__button
      background-color lookup(theme + '-switch-button-background-color')

    .x-switch--disabled
      background lookup(theme + '-switch-disabled-background-color')
      border-color lookup(theme + '-switch-disabled-border-color')
      color lookup(theme + '-switch-disabled-color')

      .x-switch__button
        background lookup(theme + '-switch-button-disabled-background-color')

    .x-switch--primary.x-switch--checked
      border-color lookup(theme + '-switch-primary-checked-border-color')
      background-color lookup(theme + '-switch-primary-checked-background-color')

    .x-switch--primary.x-switch--checked.x-switch--disabled
      border-color lookup(theme + '-switch-primary-checked-disabled-border-color')
      background-color lookup(theme + '-switch-primary-checked-disabled-background-color')
      .x-switch__button
        background lookup(theme + '-switch-button-primary-checked-disabled-background-color')

    .x-switch--success.x-switch--checked
      border-color lookup(theme + '-switch-success-checked-background-color')
      background-color lookup(theme + '-switch-success-checked-background-color')

    .x-switch--success.x-switch--checked.x-switch--disabled
      border-color lookup(theme + '-switch-success-checked-disabled-border-color')
      background-color lookup(theme + '-switch-success-checked-disabled-background-color')
      .x-switch__button
        background lookup(theme + '-switch-button-success-checked-disabled-background-color')
</style>



