<template>
  <div :class="classes" @click="handleClick">
    <span :class="getCellCls(cell)" v-for="(cell, cellIndex) in cells"><em :index="cellIndex">{{ tCell(cell.text) }}</em></span>
  </div>
</template>

<script>
// import { deepCopy } from '../../../utils/assist'
// import Locale from '../../../mixins/locale'
import cloneDeep from 'lodash/cloneDeep'

const prefixCls = 'x-date-picker-cells'

export default {
  // mixins: [ Locale ],
  props: {
    date: {},
    month: {
      type: Number
    },
    disabledDate: {},
    selectionMode: {
      default: 'month'
    }
  },
  computed: {
    classes () {
      return [
        `${prefixCls}`,
        `${prefixCls}--month`
      ]
    },
    cells () {
      let cells = []
      const cellTmpl = {
        text: '',
        selected: false,
        disabled: false
      }

      for (let i = 0; i < 12; i++) {
        // const cell = deepCopy(cellTmpl)
        const cell = cloneDeep(cellTmpl)
        cell.text = i + 1

        const date = new Date(this.date)
        date.setMonth(i)
        cell.disabled = typeof this.disabledDate === 'function' && this.disabledDate(date) && this.selectionMode === 'month'

        cell.selected = Number(this.month) === i
        cells.push(cell)
      }

      return cells
    }
  },
  methods: {
    getCellCls (cell) {
      return [
        `${prefixCls}__cell`,
        {
          [`${prefixCls}__cell--selected`]: cell.selected,
          [`${prefixCls}__cell--disabled`]: cell.disabled
        }
      ]
    },
    handleClick (event) {
      const target = event.target
      if (target.tagName === 'EM') {
        const index = parseInt(event.target.getAttribute('index'))
        const cell = this.cells[index]
        if (cell.disabled) return

        this.$emit('on-pick', index)
      }
      this.$emit('on-pick-click')
    },
    tCell (cell) {
      return `${cell}月`
    }
  }
}
</script>



