const prefixCls = 'x-picker-panel'
const datePrefixCls = 'x-date-picker'

export default {
  methods: {
    iconBtnCls (direction, type = '') {
      return [
        `${prefixCls}__icon-btn`,
        `${datePrefixCls}__${direction}-btn`,
        `${datePrefixCls}__${direction}-btn--arrow${type}`
      ]
    },
    handleShortcutClick (shortcut) {
      if (shortcut.value) this.$emit('on-pick', shortcut.value())
      if (shortcut.onClick) shortcut.onClick(this)
    },
    handlePickClear () {
      this.$emit('on-pick-clear')
    },
    handlePickSuccess () {
      this.$emit('on-pick-success')
    },
    handlePickClick () {
      this.$emit('on-pick-click')
    }
  }
}



