<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
import oneOf from '../../utils/collection/one-of'

export default {
  name: 'ButtonGroup',

  props: {
    // 尺寸
    size: {
      validator (value) {
        return oneOf(value, ['mini', 'small', 'large', 'xlarge'])
      }
    }
  },

  computed: {
    classes () {
      let baseClass = `x-btn-group`
      return [
        baseClass,
        {
          [`${baseClass}--${this.size}`]: !!this.size
        }
      ]
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
@import '../../styles/mixins/button'
for theme in themes
  .theme-{theme}
    .x-btn-group {
      .x-btn + .x-btn {
        border-left: 1px solid lookup(theme + '-btn-group-border-color')
      }
    }

.x-btn-group {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  clearfix()

  & > .x-btn {
    position: relative;
    float: left;

    &:hover,
    &:active,
    &.active {
      z-index: 2;
    }
  }

  & .x-btn--icon-only .{icon-prefix} {
    font-size: 14px;
    relative: top 1px;
  }

  &--xlarge .x-btn--icon-only .{icon-prefix} {
    font-size: 14px;
    top: 1px;
  }

  &--large .x-btn--icon-only .{icon-prefix} {
    font-size: 14px;
    top: 1px;
  }

  &--small .x-btn--icon-only .{icon-prefix} {
    font-size: 14px;
    top: 1px;
  }

  &--mini .x-btn--icon-only .{icon-prefix} {
    font-size: 14px;
    top: 1px;
  }

  // 尺寸
  &--xlarge {
    & > .x-btn {
      btn-size(btn-padding-xlarge, btn-font-size-xlarge, btn-border-radius-large)
    }
  }

  &--large {
    & > .x-btn {
      btn-size(btn-padding-large, btn-font-size-large, btn-border-radius-large)
    }
  }

  &--small {
    & > .x-btn {
      btn-size(btn-padding-small, btn-font-size, btn-border-radius-small)
    }
  }

  &--mini {
    & > .x-btn {
      btn-size(btn-padding-mini, btn-font-size, btn-border-radius-small)
    }
  }

  // 按钮、按钮组之间的排列
  .x-btn + .x-btn,
  .x-btn + &,
  & + .x-btn,
  & + & {
    margin-left: -1px;
  }

  & > .x-btn:first-child {
    margin-left: 0;
    &:not(:last-child) {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
    }
  }

  & > .x-btn:last-child:not(:first-child) {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }

  & > & {
    float: left;
  }

  & > &:not(:first-child):not(:last-child) > .x-btn {
    border-radius: 0;
  }

  & > &:first-child:not(:last-child) {
    > .x-btn:last-child {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
      padding-right: 8px;
    }
  }

  & > &:last-child:not(:first-child) > .x-btn:first-child {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
    padding-left: 8px;
  }
}
</style>



