<template>
  <div :class="classes" :style="styles">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'XCol',

  props: {
    // 栅格占据的列数
    span: {
      type: Number,
      default: 1
    },

    // 栅格左侧的间隔格数
    offset: {
      type: Number,
      default: 0
    },

    // 栅格向右移动格数
    push: {
      type: Number,
      default: 0
    },

    // 栅格向左移动格数
    pull: {
      type: Number,
      default: 0
    }
  },

  computed: {
    // 类
    classes () {
      let prefix = `x-col`

      return [
        `${prefix}-${this.span}`,
        {
          [`${prefix}--offset-${this.offset}`]: !!this.offset,
          [`${prefix}--push-${this.push}`]: !!this.push,
          [`${prefix}--pull-${this.pull}`]: !!this.pull
        }
      ]
    },

    gutter () {
      return this.$parent.gutter
    },

    styles () {
      let result = {}
      if (this.gutter - 0) {
        result.paddingLeft = this.gutter + 'px'
        result.paddingRight = this.gutter + 'px'
      }
      return result
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

$col
  position relative
  display block
  float left
  flex 0 0 auto
  box-sizing border-box
  &:first-child
    padding-left 0
  &:last-child
    padding-right 0

for i in 1..grid-columns
  .x-col-{i}
    @extend $col
    width ((100 / grid-columns) * i)%

  unless i is grid-columns
    .x-col--offset-{i}
      margin-left ((100 / grid-columns) * i)%

    .x-col--push-{i}
      left ((100 / grid-columns) * i)%

    .x-col--pull-{i}
      right ((100 / grid-columns) * i)%
</style>



