<template>
  <div :class="baseClass" :style="styles">
    <slot></slot>
  </div>
</template>

<script>
import PopperUtils from 'popper.js/dist/umd/popper-utils'
import Popper from 'popper.js'
import getStyle from '../../utils/dom/get-style'

const baseClass = `x-select-dropdown`

export default {
  name: 'Drop',

  props: {
    placement: {
      type: String,
      default: 'bottom-start'
    }
  },

  data () {
    return {
      baseClass,
      popper: null,
      width: ''
    }
  },

  computed: {
    /**
     * 容器样式
     */
    styles () {
      let style = {}
      if (this.width) {
        style.width = `${this.width}px`
      }
      return style
    }
  },

  mounted () {
    this.$on('update-popper', this.updatePopper)
    this.$on('destroy-popper', this.destroyPopper)
  },

  beforeDestory () {
    this.popper && this.popper.destroy()
  },

  methods: {
    /**
     * 更新弹出信息框
     */
    updatePopper () {
      if (this.popper) {
        this.$nextTick(() => {
          this.popper.update()
        })
      } else {
        this.$nextTick(() => {
          let options = {
            placement: this.placement,
            modifiers: {
              applyStyle: {
                gpuAcceleration: false
              },
              flip: {
                padding: 0
              },
              preventOverflow: {
                padding: 0,
                boundariesElement: 'viewport'
              }
            }
          }
          options.onCreate = popper => {
            this.resetTransformOrigin(popper.instance)
          }

          // 创建实例
          this.popper = new Popper(this.$parent.$refs.reference, this.$el, options)
        })

        if (this.$parent.$options.name === 'XSelect') {
          this.width = parseInt(getStyle(this.$parent.$el, 'width'))
        }
      }
    },

    /**
     * 销毁弹出信息框
     */
    destroyPopper () {
      if (this.popper) {
        this.resetTransformOrigin(this.popper)
        setTimeout(() => {
          this.popper.destroy()
          this.popper = null
        }, 300)
      }
    },

    /**
     * 重置CSS3动画原点
     * @param {Object} popper 弹出实例
     */
    resetTransformOrigin (popper) {
      let placementMap = {top: 'bottom', bottom: 'top'}
      let placement = popper.options.placement.split('-')[0]
      let origin = placementMap[placement]
      let transformOrigin = `center ${origin}`
      PopperUtils.setStyles(popper.popper, { transformOrigin })
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'
for theme in themes
  .theme-{theme}
    .x-select-dropdown
      background-color lookup(theme + '-select-item-bg')

.x-select-dropdown
  width inherit
  max-height 200px
  overflow auto
  margin 5px 0
  padding 5px 0
  background-color #FFF
  box-sizing border-box
  border-radius border-radius-small
  box-shadow shadow-base
  position absolute
  z-index zindex-select
  user-select none
</style>



