<template>
  <transition :name="transitionName">
    <div class="x-modal" v-show="show">
      <div class="x-modal-wrapper" @click.self="onMarkClick">
        <div class="x-modal-dialog" :style="containerStyle">
          <div class="x-modal__header">
            <slot name="header"><h3>{{ title }}</h3></slot>
          </div>
          <scroll-view class="x-modal__body">
            <slot></slot>
          </scroll-view>
          <div class="x-modal__footer" v-if="showFooter">
            <slot name="footer"></slot>
          </div>
          <div
            class="x-modal-close"
            v-if="closable"
            @click="emitClose">
            <icon type="close"></icon>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import ScrollView from '../scroll-view/scroll-view'
import Icon from '../icon/icon'

export default {
  name: 'Modal',

  components: {
    ScrollView,
    Icon
  },

  props: {
    title: {
      type: String,
      default: 'adsasfas'
    },
    show: {
      default: false
    },
    width: {
      default: '380px'
    },
    maxHeight: {
      default: '780px'
    },
    closable: {
      default: true
    },
    outsideClosable: {
      default: true
    },
    transitionName: {
      default: 'zoom'
    }
  },

  data () {
    return {
      showFooter: false
    }
  },

  computed: {
    containerStyle () {
      return {
        width: this.width,
        maxHeight: this.maxHeight
      }
    }
  },

  mounted () {
    this.$nextTick(() => {
      this.initialize()
    })
  },

  methods: {
    /**
     * 初始化
     */
    initialize () {
      this.showFooter = this.$slots.footer !== undefined
    },

    onMarkClick () {
      this.outsideClosable && this.emitClose()
    },

    emitClose () {
      this.$emit('close')
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-modal
  fixed left top
  size 100%
  z-index zindex-modal
  display table

  &-wrapper
    display table-cell
    vertical-align middle

  &-dialog
    position relative
    margin 0 auto
    width 380px
    min-height 70px
    border-radius border-radius-base

  &-close
    absolute right top
    cursor pointer
    size 30px
    text-align center
    line-height 30px
    transition color ease transition-time

  &__header
    padding 8px 15px

    h3
      font-weight normal
      margin 0

  &__body
    box-sizing border-box
    padding 15px

  &__footer
    padding 0 15px 15px
    text-align right

for theme in themes
  .theme-{theme}
    .x-modal
      background lookup(theme + '-modal-mask-bg')
      &-dialog
        background lookup(theme + '-modal-dialog-bg')
        box-shadow lookup(theme + '-modal-dialog-shadow')
      &-close
        color lookup(theme + '-close-default-color')
        &:hover
          color lookup(theme + '-close-hover-color')
</style>



