<template>
  <div :class="[baseClass]">
    <scroll-view
      :class="[baseClass + '__options']"
      v-if="data.length > 0"
      v-for="(data, dataIndex) in formatDatas"
      :key="data">
      <div
        v-for="item in data"
        :key="item"
        :class="itemClasses(item)"
        @click="onItemClick(item, dataIndex)">
        <span class="label">{{ item.label }}</span>
        <icon type="angle-right" v-show="item.hasChildren"></icon>
      </div>
    </scroll-view>
  </div>
</template>

<script>
import ScrollView from '../scroll-view/scroll-view'
import Icon from '../icon/icon'

const baseClass = `x-cascader-panel`

export default {
  name: 'CascaderPanel',

  components: {
    ScrollView,
    Icon
  },

  props: {
    data: {
      type: Array
    },
    value: {
      type: Array
    }
  },

  data () {
    return {
      baseClass
    }
  },

  computed: {
    // 格式化数据
    formatDatas () {
      let result = []
      let nextData = this.data
      this.value.forEach((val, valIndex) => {
        result[valIndex] = nextData.map((data, index) => {
          let { label, value } = data
          let { value: currentValue } = this.value[valIndex]
          let selected = false
          let hasChildren = !!(data.children && data.children.length)
          if (value === currentValue) {
            selected = true
            nextData = data.children || []
          }
          return {
            label,
            value,
            selected,
            hasChildren
          }
        })
      })
      result.push(nextData.map(data => {
        return {
          label: data.label,
          value: data.value,
          selected: false,
          hasChildren: !!(data.children && data.children.length)
        }
      }))
      return result
    }
  },

  methods: {
    /**
     * 选项样式
     * @param  {Object} item 选项
     * @return {Array}  样式列表
     */
    itemClasses (item) {
      return [
        `${this.baseClass}__options-item`,
        {
          [`${this.baseClass}__options-item--selected`]: !!item.selected,
          [`${this.baseClass}__options-item--has-children`]: item.hasChildren
        }
      ]
    },

    /**
     * 监听点击事件
     */
    onItemClick (item, dataIndex) {
      let newItem = { value: item.value, label: item.label }
      let newValue = [...this.value.slice(0, dataIndex), newItem]
      let params = {
        hasChildren: item.hasChildren,
        index: dataIndex
      }
      setTimeout(() => {
        this.$emit('valueChange', newValue, params)
      })
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

for theme in themes
  .theme-{theme}
    .x-cascader-panel
      &__options
        color lookup(theme + '-cascader-item-color')
        background-color lookup(theme + '-cascader-item-bg')
        border-right 1px solid lookup(theme + '-cascader-item-border-color')
        &-item
          &:hover
            background lookup(theme + '-cascader-item-hover-bg')
          &--selected
            color lookup(theme + '-cascader-item-selected-color')
            background lookup(theme + '-cascader-item-selected-bg')

.x-cascader-panel
  display inline-block

  &__options
    display inline-block
    min-width 100px
    height 180px
    margin 0
    vertical-align top
    box-sizing border-box

    &:last-child
      border none

    &-item
      position relative
      padding 5px 10px
      cursor pointer

      .{icon-prefix}
        absolute right 4px top 8px

      &--has-children
        .label
          margin-right 10px
</style>



