<template>
  <div :class="[baseClass]">
    <div :class="[baseClass + '__size']" v-if="!simple">
      <x-select v-model="currentPageSize" size="small" @change="onSizeChange">
        <x-option v-for="opt in sizeOptions" :value="opt" :key="opt">{{ opt }}条/页</x-option>
      </x-select>
    </div>
    <div :class="[baseClass + '__control']" v-if="!simple">
      共 {{ pages }} 页，前往
      <x-input
        size="small"
        :width="40"
        :value="this.currentPage"
        @enter="onEnter">
      </x-input>
      页
    </div>
    <div :class="[baseClass + '__total']" v-if="!simple">第 {{ range.start }}-{{ range.end }} 项，共 {{ total }} 项</div>
    <div :class="[baseClass + '__pager']">
      <button-group size="small">
        <x-button
          :disabled="isPrevDisabled"
          @click.native="prev">
          <icon type="angle-left" :size="16"></icon>
        </x-button>
        <x-button
          :disabled="isNextDisabled"
          @click.native="next">
          <icon type="angle-right" :size="16"></icon>
        </x-button>
      </button-group>
    </div>
  </div>
</template>

<script>
import ButtonGroup from '../button/button-group'
import XButton from '../button/button'
import XSelect from '../select/select'
import XOption from '../select/option'
import Icon from '../icon/icon'

const baseClass = `x-pagination`

export default {
  name: 'Pagination',

  components: {
    ButtonGroup,
    XButton,
    XSelect,
    XOption,
    Icon
  },

  props: {
    // 每页数量选项
    sizeOptions: {
      type: Array,
      default () {
        return [5, 10, 20, 50, 100]
      }
    },

    // 每页数量
    pageSize: {
      type: Number,
      default: 10
    },

    // 总数
    total: {
      type: Number
    },

    // 当前页
    current: {
      type: Number,
      default: 1
    },

    // 是否简单分页
    simple: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      baseClass,
      currentPage: this.current,
      currentPageSize: this.pageSize
    }
  },

  computed: {
    // 页数
    pages () {
      return Math.ceil(this.total / this.currentPageSize) || 1
    },
    // 记录范围
    range () {
      return {
        start: this.pageSize * (this.currentPage - 1) + 1,
        end: this.total < this.currentPageSize * this.currentPage ? this.total : this.currentPageSize * this.currentPage
      }
    },
    // 是否禁用上一页
    isPrevDisabled () {
      return this.currentPage === 1
    },
    // 是否禁用下一页
    isNextDisabled () {
      return this.currentPage === this.pages
    }
  },

  watch: {
    current (val) {
      this.currentPage = val
    },
    pageSize (val) {
      this.currentPageSize = val
    }
  },

  methods: {
    /**
     * 处理分页变化
     * @param  {Number} page 分页
     */
    onPageChange (page) {
      if (this.currentPage !== page) {
        this.currentPage = page
        this.$emit('page-update', page)
      }
    },

    /**
     * 处理每页记录数改变
     */
    onSizeChange () {
      this.$emit('size-update', this.currentPageSize)
      this.onPageChange(1)
    },

    /**
     * 处理回车键按下
     * @param  {HTML DOM Event} e 事件
     */
    onEnter (e) {
      const val = parseInt(e.target.value)
      let page = 1
      if (val > this.pages) {
        page = this.pages
      } else if (val <= 0) {
        page = 1
      } else {
        page = val
      }
      e.target.value = page
      this.onPageChange(page)
    },

    /**
     * 导航到上一页
     */
    prev () {
      const current = this.currentPage
      if (current < 1) {
        return false
      }
      this.onPageChange(current - 1)
    },

    /**
     * 导航到下一页
     */
    next () {
      const current = this.currentPage
      if (current >= this.pages) {
        return false
      }
      this.onPageChange(current + 1)
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-pagination
  &__size
  &__control
  &__total
  &__pager
    display inline-block
    vertical-align middle
</style>



