<script>
import { ICON_PREFIX } from '../../config'
import Notification from '../base/notification'

const CLASS_PREFIX = 'x'
const baseClass = `${CLASS_PREFIX}-notice`
const prefixKey = `${CLASS_PREFIX}_notice_key_`
const iconTypes = {
  'info': 'info-circle',
  'success': 'check-circle',
  'warning': 'exclamation-circle',
  'error': 'times-circle'
}

let top = 30 // 通知距离视窗顶部距离
let defaultDuration = 4.5 // 通知默认存活时长
let name = 1 // 标识
let noticeInstance // 通知实例

/**
 * 通知实例
 */
function getNoticeInstance () {
  noticeInstance = noticeInstance || Notification.newInstance({
    baseClass: baseClass,
    styles: {
      top: `${top}px`,
      right: 0
    }
  })

  return noticeInstance
}

/**
 * 创建通知实例并显示
 * @param  {String} type    通知类型 [normal|info|success|warning|error] 默认为 normal
 * @param  {Object} options 显示的参数
 * @return void
 */
function notice (type, options) {
  const title = options.title || ''
  const desc = options.desc || ''
  const noticeKey = options.name || `${prefixKey}${name}` // 用于标识实例
  const duration = (options.duration === 0) ? 0 : options.duration || defaultDuration // 通知存活时间
  const onClose = options.onClose || function () {}

  name++

  let instance = getNoticeInstance()

  let content
  const withDesc = desc === '' ? '' : ` ${baseClass}--with-desc`

  if (type === 'normal') {
    content = `
      <div class="${baseClass}--custom-content ${baseClass}--with-normal${withDesc}">
        <div class="${baseClass}__title">${title}</div>
        <div class="${baseClass}__desc">${desc}</div>
      </div>
    `
  } else {
    const iconType = iconTypes[type]
    content = `
      <div class="${baseClass}--custom-content ${baseClass}--with-icon ${baseClass}--with-${type}${withDesc}">
        <span class="${baseClass}__icon ${baseClass}__icon--${type}">
          <i class="${ICON_PREFIX} ${ICON_PREFIX}-${iconType}"></i>
        </span>
        <div class="${baseClass}__title">${title}</div>
        <div class="${baseClass}__desc">${desc}</div>
      </div>
    `
  }

  instance.notice({
    name: noticeKey.toString(),
    duration: duration,
    styles: {},
    transitionName: 'move-notice',
    content: content,
    onClose: onClose,
    closable: true
  })
}

export default {
  /**
   * 弹出通知
   * @param  {Object} options 显示的参数
   */
  open (options) {
    return notice('normal', options)
  },

  /**
   * 普通通知
   * @param  {Object} options 显示的参数
   */
  info (options) {
    return notice('info', options)
  },

  /**
   * 成功通知
   * @param  {Object} options 显示的参数
   */
  success (options) {
    return notice('success', options)
  },

  /**
   * 弹出通知
   * @param  {Object} options 显示的参数
   */
  warning (options) {
    return notice('warning', options)
  },

  /**
   * 弹出通知
   * @param  {Object} options 显示的参数
   */
  error (options) {
    return notice('error', options)
  },

  /**
   * 通知配置
   * @param {Object} options 配置项
   */
  config (options) {
    if (options.top) {
      top = options.top
    }
    if (options.duration || options.duration === 0) {
      defaultDuration = options.duration
    }
  },

  /**
   * 关闭通知
   * @param {String} name 通知唯一标识
   */
  close (name) {
    if (!name) return false
    name = name.toString()
    if (noticeInstance) {
      noticeInstance.remove(name)
    }
  },

  /**
   * 销毁通知实例
   */
  destroy () {
    let instance = getNoticeInstance()
    noticeInstance = null
    instance.destroy()
  }
}
</script>



