<template>
  <div class="table-header-icon-root" v-if="type">

    <!-- 第一种图标 排序 -->
    <div class="icon-container sort-box" v-if="type === 'sort'">
      <icon class="sort-icon" type="sort-desc" :class="{'hide': value > 0}"></icon>
      <icon class="sort-icon" type="sort-asc" :class="{'hide': value < 0}"></icon>
    </div>

    <!-- 第二种图标 tooltip -->
    <!-- <div class="icon-container tooltip-box" v-if="type === 'tooltip'">
      <tooltip class='tooltip center' v-show="!!value" :content="value" placement="top">
        <icon class="tooltip-icon" type="info-circle"></icon>
      </tooltip>
    </div> -->

  </div>
</template>

<script>
import Icon from '../icon/icon'
export default {
  name: 'TableHeaderIcon',

  components: {
    Icon
  },

  props: {
    colConfig: {
      type: Object,
      default () {
        return {}
      }
    }
  },

  computed: {
    type () {
      let result = ''
      if (this.isNumber(this.colConfig.sort)) result = 'sort'
      return result
    },

    value () {
      return this.colConfig[this.type]
    }
  },

  methods: {
    isNumber (val) {
      return val === val - 0
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.table-header-icon-root
  width 100%
  position absolute
  right 5px
  top 50%
  transform translateY(-50%)
  width 12px
  min-height 18px

  .icon-container
    position relative
    width 100%
    height 20px
    text-align center

  .sort-box
    cursor pointer
    .sort-icon
      position absolute
      top 50%
      left 50%
      transform translate(-50%, -50%) scale(1, 1.1)
      &.hide
        opacity 0.5

.center
  position absolute
  top 50%
  top 50%
  left 50%
  transform translate(-50%, -50%) scale(1, 1.1)

for theme in themes
  .theme-{theme}
    .table-header-icon-root
      .sort-icon
        color lookup(theme + '-table-header-icon-sort-icon-color')

</style>



