<template>
  <div class="x-breadcrumb">
    <a v-if="nav.length >= 2" @click="goBack" class="x-breadcrumb__back"><icon type="arrow-circle-left"></icon></a>
    <template v-for="(item, navIndex) in nav">
      <span v-if="checkLast(navIndex)" class="x-breadcrumb__label">{{ item.label }}</span>
      <a v-else @click.prevent="onLinkClick(item.link)" class="x-breadcrumb__link">{{ item.label }}</a>
      <i class="x-breadcrumb__seperator" v-if="!checkLast(navIndex)">{{ seperator }}</i>
    </template>
  </div>
</template>

<script>
import Icon from '../icon/icon'

export default {
  name: 'Breadcrumb',

  components: {
    Icon
  },

  props: {
    // 导航
    // 格式 [{link: 'foo/bar', label: 'baz'}]
    nav: {
      type: Array,
      default () {
        return []
      }
    },
    // 分隔符
    seperator: {
      type: String,
      default: '>'
    }
  },

  data () {
    return {
    }
  },

  methods: {
    /**
     * 处理链接跳转
     * @param  {String} link 链接
     */
    onLinkClick (link) {
      this.$root.$router.push(link)
    },

    /**
     * 跳转到上一层级
     */
    goBack () {
      var last = this.nav[this.nav.length - 2].link
      this.onLinkClick(last)
    },

    /**
     * 检查是否最后一个导航
     * @param  {Number} index 索引
     * @return {Boolean}
     */
    checkLast (index) {
      return index === this.nav.length - 1
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

.x-breadcrumb
  padding 5px 15px 10px
  line-height 20px
  font-size 12px
  clearfix()

  a
  span
  i
    display inline-block

  &__back
    margin-right 5px
    font-size 16px

  &__link
    margin-right 5px

  &__seperator
    margin-right 5px

for theme in themes
  .theme-{theme}
    .x-breadcrumb
      &__back
        color lookup(theme + '-breadcrumb-back-color')
        &:hover
          color lookup(theme + '-breadcrumb-back-hover-color')
      &__link
        color lookup(theme + '-breadcrumb-link-color')
      &__seperator
        color lookup(theme + '-breadcrumb-seperator-color')
</style>



