<template>
  <div :class="classes" v-show="showSpin">
    <div :class="baseClass + '__spinner'" :style="spinnerStyles">
      <svg viewBox="25 25 50 50" class="circular" :style="circularStyles">
        <circle cx="50" cy="50" r="20" fill="none" class="path" :style="pathStyles"></circle>
      </svg>
      <div class="x-spin-text" v-if="!!showText">{{ showText }}</div>
    </div>
  </div>
</template>

<script>
const PREFIX_CLASS = 'x-spin'

export default {
  name: 'Spin',

  props: {
    // 是否显示
    show: {
      type: Boolean,
      default: false
    },

    // 加载中文案
    text: {
      type: String,
      default: ''
    },

    // 是否带遮罩
    withMask: {
      type: Boolean,
      default: false
    },

    // 颜色
    color: String,

    // 大小
    size: {
      type: Number,
      default: 42
    }
  },

  data () {
    return {
      showSpin: false,
      showText: '',
      showMask: false,
      showSize: 0,
      showColor: '',
      baseClass: PREFIX_CLASS
    }
  },

  computed: {
    // 类
    classes () {
      return [
        this.baseClass,
        {
          [`${this.baseClass}--with-mask`]: this.showMask
        }
      ]
    },

    spinnerStyles () {
      return {
        marginTop: `-${this.showSize / 2}px`
      }
    },

    circularStyles () {
      return {
        width: `${this.showSize}px`
      }
    },

    pathStyles () {
      return {
        stroke: this.showColor || 'red'
      }
    }
  },
  watch: {
    show () {
      this.showSpin = this.show
    }
  },
  mounted () {
    this.showSpin = this.show
    this.showText = this.text
    this.showMask = this.withMask
    this.showSize = this.size
    this.showColor = this.color
  },
  methods: {
    start (option) {
      this.showSpin = true

      this.showText = option.text
      this.showMask = option.withMask
      this.showSize = option.size
      this.showColor = option.color
    },
    stop () {
      this.showSpin = false
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

@keyframes rotate {
  to {
    transform rotate(1turn)
  }
}

@keyframes dash {
  0% {
    stroke-dasharray 1, 150
    stroke-dashoffset 0
  }

  50% {
    stroke-dasharray 90, 150
    stroke-dashoffset -35
  }

  to {
    stroke-dasharray 90, 150
    stroke-dashoffset -124
  }
}

.x-spin
  absolute top right bottom left
  z-index zindex-loading

.x-spin__spinner
    absolute top 50%
    width 100%
    text-align center

  .circular
    animation rotate 2s linear infinite

  .path
    animation dash 1.5s ease-in-out infinite
    stroke-dasharray 1, 100
    stroke-dashoffset 0
    stroke-width 2
    stroke-linecap round


for theme in themes
  .theme-{theme}
    .x-spin
      &--with-mask
        background lookup(theme + '-mask-background-color')
</style>



