<template>
  <label :class="wrapClasses">
    <span :class="radioClasses">
      <span :class="innerClasses"></span>
      <input
        type="radio"
        :class="inputClasses"
        :disabled="disabled"
        :checked="currentValue"
        @change.stop="onChange">
    </span><slot>{{ label }}</slot>
  </label>
</template>

<script>
const baseClass = `x-radio`

export default {
  name: 'Radio',

  props: {
    // 值
    value: {
      type: Boolean,
      default: false
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false
    },
    // 标签
    label: {
      type: [String, Number]
    }
  },

  data () {
    return {
      currentValue: this.value,
      group: false // 是否处于单选框组中
    }
  },

  computed: {
    // 容器样式
    wrapClasses () {
      return [
        `${baseClass}-wrapper`,
        {
          [`${baseClass}-group-item`]: this.group,
          [`${baseClass}-wrapper--checked`]: this.currentValue,
          [`${baseClass}-wrapper--disabled`]: this.disabled
        }
      ]
    },
    // 选择框样式
    radioClasses () {
      return [
        baseClass,
        {
          [`${baseClass}--checked`]: this.currentValue,
          [`${baseClass}--disabled`]: this.disabled
        }
      ]
    },
    // 选择框样式
    innerClasses () {
      return `${baseClass}-inner`
    },
    // 原生选择框样式
    inputClasses () {
      return `${baseClass}-input`
    }
  },

  mounted () {
    if (this.$parent && this.$parent.$options.name === 'XRadioGroup') {
      this.group = true
    }
    if (!this.group) {
      this.updateValue()
    }
  },

  watch: {
    checked (val) {
      this.updateValue()
    }
  },

  methods: {
    /**
     * 处理值变化
     * @param {HTML DOM Event} event 事件
     */
    onChange (event) {
      if (this.disabled) {
        return false
      }

      const checked = event.target.checked
      this.currentValue = checked
      this.$emit('input', checked)

      if (this.group && this.label !== undefined) {
        this.$parent.onChange({
          value: this.label,
          checked: this.value
        })
      }

      if (!this.group) {
        this.$emit('change', checked)
        // TODO 在表单中使用还需要抛表单改变的事件
      }
    },

    /**
     * 更新值
     */
    updateValue () {
      this.currentValue = this.value
    }
  }
}
</script>

<style lang="stylus">
@import '../../styles/common'

for theme in themes
  .theme-{theme}
    .x-radio
      &:hover
        .x-radio-inner
          border-color lookup(theme + '-radio-hover-border-color')
      
      &-inner
        background-color lookup(theme + '-radio-bg')
        border 1px solid lookup(theme + '-radio-border-color')
      
        &:after
          background-color lookup(theme + '-radio-inner-bg')

    // 选中状态
    .x-radio--checked
      .x-radio-inner
        border-color lookup(theme + '-radio-checked-border-color')

      &:hover
        .x-radio-inner
          border-color lookup(theme + '-radio-checked-hover-border-color')

    // 禁用状态
    .x-radio--disabled
      &:hover
        .x-radio-inner
          border-color lookup(theme + '-radio-disabled-hover-border-color')

      .x-radio-inner
        border-color lookup(theme + '-radio-disabled-border-color')
        background-color lookup(theme + '-radio-disabled-bg')

        &:after
          background-color lookup(theme + '-radio-disabled-inner-bg')

      .x-radio--disabled + span
        color lookup(theme + '-radio-disabled-inner-color')

// 正常状态
.x-radio-wrapper
  font-size font-size-small
  vertical-align middle
  display inline-block
  position relative
  white-space nowrap
  margin-right 8px
  cursor pointer

  &--disabled
    cursor not-allowed

.x-radio
  display inline-block
  margin-right 4px
  white-space nowrap
  outline none
  position relative
  line-height 1
  vertical-align middle
  cursor pointer

  &-inner
    display inline-block
    size 14px
    relative top left
    border-radius 50%
    transition all transition-time ease-in-out
    box-sizing border-box

    &:after
      absolute left 2px top 2px
      content ' '
      size 8px
      border-radius 50%
      display table
      border-top 0
      border-left 0
      opacity 0
      transition all transition-time ease-in-out
      transform scale(0)
      box-sizing border-box

  &-input
    absolute left top bottom right
    z-index 1
    opacity 0
    cursor pointer

// 选中状态
.x-radio--checked
  .x-radio-inner
    &:after
      opacity 1
      transform scale(1)

// 禁用状态
.x-radio--disabled
  cursor not-allowed

  .x-radio-input
    cursor not-allowed

span.x-radio + *
  margin-left 2px
  margin-right 2px
</style>



