/**
 * 判断参数值是否列表其中之一
 * @param {Number|String|Boolean} value 参数值
 * @param {Array} validList 合法值列表
 * @return {Boolean}
 */
function oneOf (value, validList) {
  for (let i = 0; i < validList.length; i++) {
    if (value === validList[i]) {
      return true
    }
  }
  return false
}

export default oneOf



