import Vue from 'vue'
import App from './app'
import router from './router'

// 全局组件
import components from '@/main'

// 指令
import filters from './filters'

// 指令

Vue.config.productionTip = false

// 注册过滤器
// ------------------------------
Object.keys(filters).forEach((key) => {
  Vue.filter(key, filters[key])
})

// 注册全局组件
Vue.use(components)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



