<template>
  <div
    class="table-header-cell-root"
    :class="colConfig.class"
    v-html="colConfig.label"
    :style="{cursor: colConfig.pointer ? 'pointer' : ''}"
    ></div>
</template>

<script>
export default {
  name: 'TableHeaderCell',

  props: {
    colConfig: {
      type: Object,
      default () {
        return {}
      }
    }
  }
}
</script>

<style lang="stylus">

.table-header-cell-root
  padding 5px 10px
</style>



